import { Component, OnInit, forwardRef, Input, Output, EventEmitter } from '@angular/core';
import { NG_VALUE_ACCESSOR, FormControl } from '@angular/forms';
import { Subscription, debounceTime } from 'rxjs';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { EntidadDireccion } from '../../../interface/EntidadDireccion.interface';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { filter } from 'rxjs/operators';

@Component({
  selector: 'app-entidad-direccion-autocomplete',
  templateUrl: './entidad-direccion-autocomplete.component.html',
  styleUrls: ['./entidad-direccion-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => EntidadDireccionAutocompleteComponent),
      multi: true
    }
  ]
})
export class EntidadDireccionAutocompleteComponent implements OnInit {

  @Input() placeholder: string;
  appearance: string = '';
  @Output() onSelectedChange = new EventEmitter();
  @Input() idTipoEntidad: number = 1;
  opcionesSubs: Subscription;
  opcionesFiltradas = [];
  text = new FormControl()
  value;
  isDisabled: boolean;
  subs: Subscription;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(entidadDireccion: EntidadDireccion): void {
    if (entidadDireccion) {
      this.seleccionar(entidadDireccion, { isUserInput: true })
    }
    else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(value: string) {
    if( typeof(value) === 'string' ){

      let filters: any = [
        { "filters[direccion][like]": `%${value}%`  }
      ]

      if( this.idTipoEntidad ){
        filters.push(  { 'filters[$Entidad.idTipoEntidad$][eq]': this.idTipoEntidad } )
      }

      this.opcionesFiltradas = <any>await this._cs.getCatalogo('entidadDireccion', filters , "direccion", 10, 0);
    }
  }
  constructor(
    public _cs: CatalogosService,
    private _utls: UtilitiesService
  ) {
    this.appearance = this._utls.appearance;
  }
  ngOnDestroy(): void {
    if (this.subs) this.subs.unsubscribe()
  }

  ngOnInit() {
    this.subs = this.text.valueChanges.pipe(
      filter((value: any ) => {
        if (typeof(value) === 'object') return false;
        return value !== null;
      }),
      debounceTime(400)
    ).subscribe(text => this.onInput(text))
  }
  seleccionar(entidad: EntidadDireccion, event) {
    if (event.isUserInput) {
      this.value = entidad;
      this.text.setValue(entidad, { emitEvent: false })
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(entidad);
    }
  }
  clearValue() {
    this.value = null;
    this.text.setValue("", { emitEvent: false })
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(this.value);

  }
  handleBlur() {
    if (!this.value || !this.value.value) {
      this.clearValue();
    } else {
      let { text } = this.value;
      let { nombre } = this.value.value;
      if (text !== nombre) {
        this.text.setValue(nombre, { emitEvent: false })
      }
    }
  }
  displayFn( option ): any {
    return option && option.direccion 
      ? `${option.Entidad.razonSocial} - ${option.direccion}`
      : '';
  }
}
