import { Component, OnInit, forwardRef, Input, EventEmitter, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { TipoEntidad } from '../../../interface/TipoEntidad';
import { Subscription } from 'rxjs';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { MatSelectChange } from '@angular/material/select';

@Component({
  selector: 'app-tipo-entidad-dropdown',
  templateUrl: './tipo-entidad-dropdown.component.html',
  styleUrls: ['./tipo-entidad-dropdown.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoEntidadDropdownComponent),
      multi: true
    }
  ]
})
export class TipoEntidadDropdownComponent implements OnInit {

  @Input() idTipoEntidad: number[] = [];
  @Input() placeholder: string;
  appearance;
  value: TipoEntidad;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipo: TipoEntidad): void {
    if (tipo) {
      this.value = tipo;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  ngOnDestroy(): void {
    this.tipoEntidad$.unsubscribe();
  }

  tipoEntidad$: Subscription;

  opciones: TipoEntidad[];
  @Output() onSelectedChange = new EventEmitter();

  constructor(
    private _cs: CatalogosService,
    private _ut: UtilitiesService
  ) { }

  ngOnInit(): void {
    this.appearance = this._ut.appearance;
    this.inicializar();
  }
  async inicializar() {
    this.tipoEntidad$ = this._cs.tiposEntidad$.subscribe(tipos => {

      if (this.idTipoEntidad.length > 0) {
        this.opciones = tipos.filter(itm => this.idTipoEntidad.includes(itm.idTipoEntidad))

      } else {
        this.opciones = tipos;
      }
      if (this.value) {
        this.writeValue(this.value);
      }
    });
    await this._cs.loadTipoEntidad();
  }

  seleccionar(event: MatSelectChange) {
    let opt = this.opciones.find(item => item.idTipoEntidad === event.value);
    this.value = opt;
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(opt);
  }
  resetValue() {
    this.value = this.opciones[0];
  }

}
