import { Component } from '@angular/core';
import { SwUpdate, VersionReadyEvent } from '@angular/service-worker';
import { DialogService } from './services/dialog/dialog-v2.service';
import { filter, map } from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.sass']
})
export class AppComponent {
  title = 'SMARTLOG';

  constructor(private swUpdate: SwUpdate, private _ds: DialogService) {
    // if (this.swUpdate.activateUpdate) {
    //   console.log("si");
    //   this.swUpdate.activateUpdate().then(async () => {
    //     let resp = await this._ds.askFor("Actualizacin detectada", "Se ha detectado una nueva actualizacin.");

    //   })
    // }
    this.checkForUpdates();
  }
  public checkForUpdates(): void {
    this.swUpdate.versionUpdates.
      pipe(filter((evt): evt is VersionReadyEvent => evt.type === 'VERSION_READY'),
        map(evt => ({
          type: 'UPDATE_AVAILABLE',
          current: evt.currentVersion,
          available: evt.latestVersion,
        }))).subscribe(async (evt) => {
          console.log(evt);
          let resp = await this._ds.askFor("Actualizacin detectada", "Se ha detectado una nueva actualizacin.");
          if (resp) {
            window.location.reload()
          }
          //this.promptUser();
        });
  }
}
