  async loadLugaresEntrega() {
    try {
      if (!this.catalogos.lugaresEntregaLoaded) {
        const lugaresEntrega = <any>await this.getLugaresEntrega();
        this.catalogos.lugaresEntregaLoaded = true;
        this.catalogos.lugaresEntrega = lugaresEntrega;
      }
      this.lugaresEntregaSubject.next(this.catalogos.lugaresEntrega);
    } catch (error) {
      throw error;
    }
  }

