import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { EntidadService } from '../../../../services/entidad/entidad.service';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-direccion-picker',
  templateUrl: './direccion-picker.component.html',
  styleUrls: ['./direccion-picker.component.sass']
})
export class DireccionPickerComponent implements OnInit {

  @Input() idEntidad;
  direcciones = [];
  direccion = new FormControl;
  @Output() onSelectionChange = new EventEmitter()
  constructor(private _es: EntidadService) { }

  ngOnInit(): void {

    this.direccion.valueChanges.subscribe(itm => {
      this.onSelectionChange.emit(itm);
    })
    this.getEntidad()



  }
  async getEntidad() {
    try {
      let entidad = <any>await this._es.getEntidad(this.idEntidad);
      this.direcciones = entidad.Direcciones;


    } catch (error) {

    }
  }


}
