import { Component, OnInit, Input, Optional, Inject } from '@angular/core';
import { DigitalService } from '../../../../../services/digital/digital.service';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-img-viewer',
  templateUrl: './img-viewer.component.html',
  styleUrls: ['./img-viewer.component.sass']
})
export class ImgViewerComponent implements OnInit {

  urlImg;
  zoomImg: number = 1;
  @Input() btnZoom: boolean = false;
  @Input() printPDF: boolean = false;
  @Input() btnZoomIn: boolean = false;
  @Input() btnZoomOut: boolean = false;
  @Input() btnPrint: boolean = false;
  @Input() title: string = '';
  @Input() option: number = 3;
  @Input() nameFile: string = '';

  constructor(
    private _ds: DigitalService,
    @Optional() public dialogRef: MatDialogRef<ImgViewerComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data
  ) {
    if (data) {
      this.option = data.option;
      this.urlImg = data.urlImg;
      this.printPDF = data?.printPDF;
      this.btnZoom = data?.btnZoom;
      this.title = data?.title;
      this.nameFile = data?.nameFile;
    }
  }

  ngOnInit(): void {
  }

  handleZoom(zoom) {
    this.zoomImg += zoom;
  }
  async downloadImg() {
    try {
      let tempLink = document.createElement('a');
      tempLink.href = this.urlImg;
      tempLink.setAttribute('download', this.nameFile);
      tempLink.click();
    } catch (err) {
      console.log(err);
    }
  }

}
