import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-skeletor-list-checkbox',
  templateUrl: './skeletor-list-checkbox.component.html',
  styleUrls: ['./skeletor-list-checkbox.component.sass']
})
export class SkeletorListCheckboxComponent implements OnInit {

  @Input() count:number = 1;
  values;

  constructor() { }

  ngOnInit(): void {
    this.values = Array(this.count);
  }

}
