import { Component, OnInit, ViewChild, Inject, Optional, EventEmitter } from '@angular/core';
import { MatSort } from '@angular/material/sort';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { TraficoService } from '../../../../services/trafico/trafico.service';
import { SelectionModel } from '@angular/cdk/collections';

@Component({
  selector: 'app-trafico-busqueda',
  templateUrl: './trafico-busqueda.component.html',
  styleUrls: ['./trafico-busqueda.component.sass']
})
export class TraficoBusquedaComponent implements OnInit {

  displayedColumns = ["select", "trafico", "shipper", "consignatario"]
  isFetching = false;
  embarques = []
  resultsLength = 0;
  pageSize = 10;
  filtros;
  seleccion = 1;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  constructor(
    private _ts: TraficoService,
    public dialogRef: MatDialogRef<TraficoBusquedaComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data

  ) {
    this.filtros = data.filtros;

  }

  ngOnInit(): void {
    this.getTraficos()
  }
  async getTraficos() {
    try {
      let embarques = <any>await this._ts.getTraficos(this.filtros);
      embarques = embarques.map(itm => ({ ...itm, shipper: itm?.Shipper?.razonSocial, consignatario: itm?.Consignatario?.razonSocial }))
      this.embarques = embarques;
    } catch (error) {

    }
  }

  handleProcesarClick(embarque) { }
  handleAceptarClick() {
    this.dialogRef.close(this.selection.selected)
  }

  //onSelectionChange = new EventEmitter();
  selection = new SelectionModel<any>(true, []);
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.embarques.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.embarques.forEach(row => this.selection.select(row));
    //this.onSelectionChange.emit(this.selection.selected);
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.position + 1}`;
  }
  handlCheckedChange(event, row) {
    this.selection.clear();
    event ? this.selection.toggle(row) : null
  }
}
