import { Injectable } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ConfirmDialogData } from 'src/app/shared/classes/confirmDialogData';
import { ConfirmDialogComponent } from 'src/app/shared/components/common/modals/confirm-dialog/confirm-dialog.component';
import { ModalDialogData, ModalDialogDataConfig } from '../../shared/classes/confirmDialogData';
import { ModalDialogComponent } from '../../shared/components/common/modals/modal-dialog/modal-dialog.component';
import { ModalOptionPickerComponent } from '../../shared/components/common/modals/modal-option-picker/modal-option-picker.component';

@Injectable({
  providedIn: 'root'
})
export class DialogService {

  constructor(
    private _matSnackBar: MatSnackBar,
    public dialog: MatDialog) { }

  async askFor(title: string, msg: string, keys = true) {
    let okButton = "OK";
    let cancelButton = "Cancel"
    const dialog = this.openConfirmDialog(
      new ConfirmDialogData(title, msg, okButton, cancelButton)
    );
    return new Promise(resolve => {
      dialog.afterClosed().subscribe(result => {
        resolve(result);
      });
    });
  }
  async showModal(title: string, msg: string, config: ModalDialogDataConfig = null) {
    if (!config) {
      config = { okButton: "OK", cancelButton: "Cancel", showOkButton: true, showCancelButton: true }
    }
    const dialog = this.openModalDialog(
      new ModalDialogData(title, msg, config)
    );
    return new Promise(resolve => {
      dialog.afterClosed().subscribe(result => {
        resolve(result);
      });
    });
  }
  async showModalOptions(title: string, msg: string, opciones, config: ModalDialogDataConfig = null) {
    if (!config) {
      config = { okButton: "OK", cancelButton: "Cancel", showOkButton: true, showCancelButton: true }
    }
    const dialog = this.openModalOptionDialog(
      new ModalDialogData(title, msg, config, opciones)
    );
    return new Promise(resolve => {
      dialog.afterClosed().subscribe(result => {
        resolve(result);
      });
    });
  }
  public async showSnack(message: string, action: string = null, type: string = "done") {
    return this._matSnackBar.open(message, action, {
      duration: 6000,
      panelClass: `snack-${type}`
    });
  }
  openConfirmDialog(dialogData: ConfirmDialogData) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.data = { confirmDialogData: dialogData };
    return this.dialog.open(ConfirmDialogComponent, dialogConfig);
  }
  openModalDialog(dialogData: ModalDialogData) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.data = { modalDialogData: dialogData };
    return this.dialog.open(ModalDialogComponent, dialogConfig);
  }

  openModalOptionDialog(dialogData: ModalDialogData) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.data = { modalDialogData: dialogData };
    return this.dialog.open(ModalOptionPickerComponent, dialogConfig);
  }
}
