import { Directive,
  Output,
  Input,
  EventEmitter,
  HostBinding,
  HostListener } from '@angular/core';

@Directive({
  selector: '[appDnd]'
})
export class DndDirective {

  constructor() { }
  
  @Output() fileDropped = new EventEmitter<any>();
  @Output() dragEnd     = new EventEmitter<any>();
  @Output() dragOver    = new EventEmitter<any>();
  @Output() dragEnter   = new EventEmitter<any>();
  @Output() dragDrop    = new EventEmitter<any>();

  // Dragover listener
  @HostListener('dragenter', ['$event']) onDragEnter(evt) {
    evt.preventDefault();
    evt.stopPropagation();
    this.dragEnter.emit(evt);
  }

  // Dragover listener
  @HostListener('dragover', ['$event']) onDragOver(evt) {
    evt.preventDefault();
    evt.stopPropagation();
    this.dragOver.emit(evt);
  }

  // Dragleave listener
  @HostListener('dragleave', ['$event']) public onDragLeave(evt) {
    evt.preventDefault();
    evt.stopPropagation();
    this.dragEnd.emit(evt);
  }

  // Drop listener
  @HostListener('drop', ['$event']) public ondrop(evt) {
    evt.preventDefault();
    evt.stopPropagation();
    let files = evt.dataTransfer.files;
    if (files.length > 0) {
      this.fileDropped.emit(files);
    }else{
      this.dragDrop.emit(evt);
    }
  }
}
