import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
//import { LoginComponent } from './modules/login/login/login/login.component';

import { AuthGuard } from './services/guard/auth.guard';
import { MainComponent } from './modules/main/main.component';



const routes: Routes = [

  { path: "login", loadChildren: () => import('./modules/login-main/login-main.module').then(m => m.LoginMainModule) },
  { path: "", component: MainComponent, canActivate: [AuthGuard], loadChildren: () => import('./modules/main/main.module').then(m => m.MainModule) },//

];

@NgModule({
  imports: [RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
