import { ComponentType } from '@angular/cdk/portal';
import { Injectable } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { firstValueFrom } from 'rxjs';
import { ConfirmDialogData } from '../../shared/classes/confirmDialogData';
import { ConfirmDialogComponent } from '../../shared/components/common/modals/confirm-dialog/confirm-dialog.component';

@Injectable({
  providedIn: 'root'
})
export class DialogService {

  constructor(
    private _matSnackBar: MatSnackBar,
    private dialog: MatDialog,
  ) { }

  async askFor(title: string, msg: string, keys = true) {
    let okButton = "OK";
    let cancelButton = "Cancel"
    const dialog = this.openConfirmDialog(
      new ConfirmDialogData(title, msg, okButton, cancelButton)
    );
    return new Promise(resolve => {
      dialog.afterClosed().subscribe(result => {
        resolve(result);
      });
    });
  }
  async openModalDialogComponent( component:ComponentType<unknown>, data = null, dialogConf = {}){
    let dialogConfig = new MatDialogConfig();
    dialogConfig = { ...dialogConfig, autoFocus: false, disableClose: true, ...dialogConf }
    if (data) dialogConfig.data = data;
    return await firstValueFrom( this.dialog.open( component, dialogConfig  ).afterClosed() );
  }
  async openModalFullComponent( component:ComponentType<unknown>, data = null ){
    let dialogConfig = new MatDialogConfig();
    dialogConfig = { ...dialogConfig, autoFocus: false, disableClose: false, panelClass: "fullscreen-dialog", maxWidth: "none" }
    if (data) dialogConfig.data = data;
    return await firstValueFrom( this.dialog.open( component, dialogConfig ).afterClosed() );
  }
  public async showSnack(message: string, action: string = null, type: string = "done") {
    return this._matSnackBar.open(message, action, {
      duration: 6000,
      panelClass: `snack-${type}`
    });
  }
  openConfirmDialog(dialogData: ConfirmDialogData) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.data = { confirmDialogData: dialogData };
    return this.dialog.open(ConfirmDialogComponent, dialogConfig);
  }
}
