import { Component, OnInit, ViewChild, Inject, Optional, EventEmitter } from '@angular/core';
import { MatSort } from '@angular/material/sort';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

import { SelectionModel } from '@angular/cdk/collections';
import { EntradaService } from '../../../../services/entrada/entrada.service';


@Component({
  selector: 'app-entrada-busqueda',
  templateUrl: './entrada-busqueda.component.html',
  styleUrls: ['./entrada-busqueda.component.sass']
})
export class EntradaBusquedaComponent implements OnInit {

  displayedColumns = ["select", "entrada", "shipper", "consignatario", "fechaArribo", "bl", "bultos", "pesoLbs"]//
  isFetching = false;
  entradas = []
  resultsLength = 0;
  pageSize = 10;
  filtros;
  seleccion = 1;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  constructor(
    private _es: EntradaService,
    public dialogRef: MatDialogRef<EntradaBusquedaComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data

  ) {

    this.filtros = data.filtros;

  }

  ngOnInit(): void {
    this.getEntradas()
  }
  async getEntradas() {
    try {
      let entradas = <any>await this._es.getEntradas(this.filtros);
      entradas = entradas.map(itm => ({ ...itm, shipper: itm?.Shipper?.razonSocial, consignatario: itm?.Consignatario?.razonSocial }))
      this.entradas = entradas;
    } catch (error) {

    }
  }

  handleProcesarClick(entrada) { }
  handleAceptarClick() {
    this.dialogRef.close(this.selection.selected)
  }

  //onSelectionChange = new EventEmitter();
  selection = new SelectionModel<any>(true, []);
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.entradas.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.entradas.forEach(row => this.selection.select(row));
    //this.onSelectionChange.emit(this.selection.selected);
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(row) ? 'deselect' : 'select'} row ${row.position + 1}`;
  }
  handlCheckedChange(event, row) {
    this.selection.clear();
    event ? this.selection.toggle(row) : null
  }

}
