import { Component, Inject, OnInit, Optional } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-usuario-password',
  templateUrl: './usuario-password.component.html',
  styleUrls: ['./usuario-password.component.sass']
})
export class UsuarioPasswordComponent implements OnInit {

  form: FormGroup;
  constructor(
    public dialogRef: MatDialogRef<UsuarioPasswordComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data
  ) { }

  ngOnInit(): void {
    this.form = new FormGroup({
      password: new FormControl(null, [Validators.required])
    })
  }
  async handleAceptarClick() {
    // Confirmar contrasea
    this.dialogRef.close({ ok: true });
  }

}
