import { Component, OnInit, forwardRef, Input, EventEmitter, Output, OnDestroy } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subscription } from 'rxjs';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { MatSelectChange } from '@angular/material/select';

@Component({
  selector: 'app-departamento-dropdown',
  templateUrl: './departamento-dropdown.component.html',
  styleUrls: ['./departamento-dropdown.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DepartamentoDropdownComponent),
      multi: true
    }
  ]
})
export class DepartamentoDropdownComponent implements OnInit,OnDestroy {

  @Input() idOficina$ = new EventEmitter();
  idOficina;
  departamentos = [];
  @Input() placeholder: string;
  appearance;
  value;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipo): void {
    if (tipo) {
        this.value = tipo;
    } else {
        this.value = null;
    }
  }
  registerOnChange(fn: any): void {
      this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
      this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
      this.isDisabled = isDisabled;
  }

  ngOnDestroy(): void {
    this.departamentoSubs.unsubscribe();
    this.idOficinaSubs.unsubscribe();
  }

  departamentoSubs   : Subscription;
  TipoConcepto$ : Subscription;
  idOficinaSubs: Subscription;

  opciones;
  opcionesFiltradas;
  @Output() onSelectedChange = new EventEmitter();

  constructor(
      private _cs: CatalogosService,
      private _ut:UtilitiesService
  ) { }

  ngOnInit(): void {
      this.appearance     = this._ut.appearance;
      this.inicializar();

      this.idOficinaSubs = this.idOficina$.subscribe( idOficina =>{
          if( this.opciones ){
            this.opcionesFiltradas = this.opciones.filter(op=>op.idOficina === idOficina);
          }
      })
  }
  async inicializar() {
    this.departamentoSubs = this._cs.departamento$.subscribe(tipos => {
        this.departamentos = tipos;
        this.opciones = tipos;
        if (this.value) {
          this.opcionesFiltradas = tipos;
          this.writeValue(this.value);
        }
    });
    await this._cs.loadDepartamento();
  }

  seleccionar(event: MatSelectChange) {
      let opt = this.opcionesFiltradas.find(item => item.idDepartamento === event.value);
      this.value = opt;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(opt);
  }
  resetValue() {
      this.value = this.opciones[0];
  }

}
