export class ModalDialogData {
  title: string;
  message: string;
  config: ModalDialogDataConfig;
  opciones: Array<any>;
  constructor(
    title: string,
    message: string,
    config: ModalDialogDataConfig,
    opciones = null

  ) {
    this.title = title;
    this.message = message;
    this.config = config;
    this.opciones = opciones;
  }
}

export class ModalDialogDataConfig {
  showOkButton: boolean;
  showCancelButton: boolean;
  okButton: string;
  cancelButton: string;
}