import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { URL } from 'src/app/config/url.services';

@Injectable({
  providedIn: 'root'
})
export class DigitalService {

  constructor(private _http: HttpClient,) {

  }

  async getDocumentos(filters = null, sort = null, size = null, page = null) {
    let params = [];
    if (filters) params = filters ? filters.map(item => `${Object.keys(item)[0]}=${Object.values(item)[0].toString().replace("&", "%26")}`) : []
    if (sort) params.push("sort=" + sort);
    if (size) params.push("size=" + size);
    if (page) params.push("page=" + page);
    //escape &
    const queryStr = params.join("&");
    const response = await this._http.get(`${URL}/digital/documento${queryStr ? "?" + queryStr : ""}`).toPromise();
    return response;
  }
  async getReferencia(referencia) {
    const response = await this._http.get(`${URL}/digital/referencia?referencia=${referencia}`).toPromise();
    return response;
  }
  async postDocumento(archivo, config) {
    const response = await this._http.post(`${URL}/digital/documento`, archivo).toPromise();//, /*{ onUploadProgress: config }*/);
    return response;
  }
  async descargarDocumento(idDocumento) {
    const response = await this._http.get(`${URL}/digital/documento/${idDocumento}/descargar`, { responseType: 'blob' as 'json' }).toPromise();//, /*{ onUploadProgress: config }*/);
    return response;
  }
  async descargarZip(documentos, responseType) {
    const response = await this._http.post(`${URL}/digital/zip`, documentos, { responseType, observe: 'response' }).toPromise();//, /*{ onUploadProgress: config }*/);
    return response;
  }
  async patchDocumento(idDocumento, data) {
    const response = await this._http.patch(`${URL}/digital/documento/${idDocumento}`, data).toPromise();//, /*{ onUploadProgress: config }*/);
    return response;
  }
  async deleteDocumento(idDocumento) {
    const response = await this._http.delete(`${URL}/digital/documento/${idDocumento}`).toPromise();//, /*{ onUploadProgress: config }*/);
    return response;
  }
  async getImagenMiniatura(idDocumento) {
    const response = await this._http.post(`${URL}/digital/documento/${idDocumento}/miniatura`, null).toPromise();//, /*{ onUploadProgress: config }*/);
    return response;
  }
}
