import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-images-preview',
  templateUrl: './images-preview.component.html',
  styleUrls: ['./images-preview.component.sass']
})
export class ImagesPreviewComponent implements OnInit {

  @Input() images = [];
  @Output() onChangeFiles = new EventEmitter();
  extensionImages = ['png','jpeg','jpg','gif','bmp','tiff'];

  constructor(
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit(): void {
  }

  safeUrl(url){
    return this.sanitizer.bypassSecurityTrustResourceUrl(url);
  }

  async onFileSelected($event,files = []) {
    let filesSelected = files.length ? files : $event.target?.files;
    let archivos   = [];

    if(filesSelected.length){
      let lastId = this.images[this.images?.length - 1]?.id || 0;
      for (let i = 0; i < filesSelected.length; i++) {
        const file: File = filesSelected[i];
        let archivo:any = {
          id: lastId + (i + 1),
          tamano: (file.size / (1024 ** 2)).toFixed(2),
          nombreOriginal: `${file.name}`,
          extension: `${this.getExtension(file.name)}`,
          file
        }
        const fileURL = URL.createObjectURL(file);
        archivo.urlPreview = fileURL;
        archivos.push(archivo);
      }
      this.images = [ ...archivos,...this.images ];
      this.onChangeFiles.emit({
        accion: 'change',
        archivos: this.images
      });
    }
  }

  getExtension(fileName){
    return fileName.split('.').pop();
  }

  deletedFile(archivo){
    this.onChangeFiles.emit({ accion: 'deleted',archivos: [ archivo ]})
  }

}
