import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-icon-name-initials',
  templateUrl: './icon-name-initials.component.html',
  styleUrls: ['./icon-name-initials.component.sass']
})
export class IconNameInitialsComponent implements OnInit {

  @Input() name   :string  = '';
  @Input() width  :number  = 35;
  @Input() height :number  = 35;
  @Input() bColor :string  = 'primary'
  @Input()fontSize:number  = 15;
  @Input() fontWeigth:number = 300;
  // @Input() opacity=null;
  public nameInitials!: string;

  constructor(
  ) { }

  ngOnInit(): void {
    this.nameInitials = this.getFirstTwoLetters(this.name);
  }

  public getFirstTwoLetters(str: string) {
    return str
      .split(' ')
      .map((word, i) => {
        if (i <= 1) return word[0]
      })
      .join('');
  };
}
