import { Component, OnInit, Input, forwardRef, EventEmitter, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { TipoTerminoPago } from '../../../interface/TipoTerminoPago';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { Subscription } from 'rxjs';
import { MatSelectChange } from '@angular/material/select';

@Component({
  selector: 'app-tipo-termino-pago-dropdown',
  templateUrl: './tipo-termino-pago-dropdown.component.html',
  styleUrls: ['./tipo-termino-pago-dropdown.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoTerminoPagoDropdownComponent),
      multi: true
    }
  ]
})
export class TipoTerminoPagoDropdownComponent implements OnInit {

  @Input() placeholder: string;
  appearance;
  value:  TipoTerminoPago;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipo:  TipoTerminoPago): void {
    if (tipo) {
        this.value = tipo;
    } else {
        this.value = null;
    }
  }
  registerOnChange(fn: any): void {
      this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
      this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
      this.isDisabled = isDisabled;
  }

  ngOnDestroy(): void {
      this.tipoTerminoPago$.unsubscribe();
  }

  tipoTerminoPago$ : Subscription;

  opciones: TipoTerminoPago[];
  @Output() onSelectedChange = new EventEmitter();

  constructor(
      private _cs: CatalogosService,
      private _ut:UtilitiesService
  ) { }

  ngOnInit(): void {
      this.appearance     = this._ut.appearance;
      this.inicializar();
  }
  async inicializar() {
    this.tipoTerminoPago$ = this._cs.tiposTerminoPago$.subscribe(tipos => {
      
        this.opciones = tipos
        if (this.value) {
            this.writeValue(this.value);
        }
    });
    await this._cs.loadTipoTerminoPago();
  }

  seleccionar(event: MatSelectChange) {
      let opt = this.opciones.find(item => item.idTipoTerminoPago === event.value);
      this.value = opt;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(opt);
  }
  resetValue() {
      this.value = this.opciones[0];
  }

}
