import { Component, OnInit, forwardRef, Input, Output, EventEmitter } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { TipoTrafico } from '../../../classes/tipoTrafico.class';
import { Subscription } from 'rxjs';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { MatSelectChange } from '@angular/material/select';

@Component({
  selector: 'app-tipo-trafico-dropdown',
  templateUrl: './tipo-trafico-dropdown.component.html',
  styleUrls: ['./tipo-trafico-dropdown.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoTraficoDropdownComponent),
      multi: true
    }
  ]
})
export class TipoTraficoDropdownComponent implements OnInit {

  tiposTraficos: TipoTrafico[] = [];
  @Input() placeholder: string;
  appearance;
  value:  TipoTrafico;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipo:  TipoTrafico): void {
    if (tipo) {
        this.value = tipo;
    } else {
        this.value = null;
    }
  }
  registerOnChange(fn: any): void {
      this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
      this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
      this.isDisabled = isDisabled;
  }

  ngOnDestroy(): void {
      this.tipoTraficoSubs.unsubscribe();
  }

  tipoTraficoSubs   : Subscription;
  tipoTrafico$ : Subscription;

  opciones: TipoTrafico[];
  @Output() onSelectedChange = new EventEmitter();

  constructor(
      private _cs: CatalogosService,
      private _ut:UtilitiesService
  ) { }

  ngOnInit(): void {
      this.appearance     = this._ut.appearance;
      this.inicializar();
  }
  async inicializar() {
    this.tipoTraficoSubs = this._cs.tiposTrafico$.subscribe(tipos => {
        this.tiposTraficos = tipos;
        this.opciones = tipos
        if (this.value) {
            this.writeValue(this.value);
        }
    });
    await this._cs.loadTiposTraficos();
  }

  seleccionar(event: MatSelectChange) {
      let opt = this.opciones.find(item => item.idTipoTrafico === event.value);
      this.value = opt;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(opt);
  }
  resetValue() {
      this.value = this.opciones[0];
  }

}
