import { Injectable } from '@angular/core';
import { UserConfig } from '../../shared/classes/userConfig';
import { Subject, throwError } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { tap, map } from 'rxjs/operators';
import { StorageService } from '../storage/storage.service';
import { Router } from '@angular/router';
import { Profile } from '../../shared/classes/profile.class';
import { URL } from '../../config/url.services';


@Injectable({
  providedIn: 'root'
})
export class UsuarioService {
  usuario: UserConfig;
  perfilLoaded = false;
  perfil: Profile = null;
  private menuSubject = new Subject<any>();
  menu$ = this.menuSubject.asObservable();

  get getNombreCompleto(){
    return `${this.usuario.nombre}${ this.usuario.apPaterno ? ` ${this.usuario.apPaterno}` : '' }${ this.usuario.apMaterno ? ` ${this.usuario.apMaterno}` : '' }`
  }

  constructor(
    private _http: HttpClient,
    private _ss: StorageService,
    private _router: Router,
  ) {
    this._ss.loadUsrConfig();
    if (this.isAuthenticated()) {
      this.usuario = { ...this._ss.usrConfig }
    }
  }
  public isAuthenticated(): boolean {
    let token = this._ss.getUsrPreference("token");
    if (token) return true;
    return false;
  }
  public setUserConfig(userData) {
    this._ss.loadUsrConfig();
    this.usuario = { ...userData }
  }
  menu: any;
  routes = [];
  async getMenu() {
    let response = <any>await this._http.get(`${URL}/usuario/menuMobile`).toPromise();
    this.routes = [];
    response.forEach(menu => {
      if (menu.subMenu) {
        menu.opciones = menu.subMenu.filter(itm => itm.idTipoMenu === 2);
        menu.subMenu = menu.subMenu.filter(itm => itm.idTipoMenu === 1);
      }
    });
    this.menu = response
    this.getRutasValidas(this.menu);
    this.menuSubject.next(this.menu);
    return this.menu;

  }
  getRutasValidas(menu) {
    menu.forEach(itm => {
      let { idMenu, nombre, icono, url, subMenu, opciones } = itm
      this.routes.push({ idMenu, nombre, icono, url })
      if (subMenu.length > 0) {
        this.getRutasValidas(subMenu);
      }
      if (opciones?.length > 0) {
        this.getRutasValidas(opciones);
      }

    });
    this.routes.push({ idMenu: 0, nombre: "Perfil", icono: "", url: "/perfil" })
  }
  ///////////////////////////////// Login
  async login(loginUser: any) {
    const response = await this._http.post(`${URL}/auth`, loginUser).toPromise();
    return response;
  }
  async logout(redirect: boolean = false) {
    const prom = new Promise((resolve, reject) => {
      try {
        this.perfil = null;
        this._ss.clearUsrConfig();
        this.perfilLoaded = false;
        this.usuario = null;
        //this._ns.resetNotifications();
        // this.catalogos = {
        //   oficinasLoaded: false,
        //   oficinas: [],
        //   patentesLoaded: false,
        //   patentes: [],
        //   aduanasLoaded: false,
        //   aduanas: [],
        //   tiposTrasnporteDisponibles: null,
        //   filtrosReferenciaDisponibles: null
        // }

        if (redirect) this._router.navigate(["/login"]);
        resolve(true);
      } catch (e) {
        reject(e);
      }
    });
    return prom;
  }

  refreshToken = () => {
    const { refresh, idUsuario, tipoSeleccionado } = this.usuario;
    if (!refresh) {
      return throwError(new Error("Token not found"));
    }
    return this._http.post(`${URL}/auth/refresh`, { refresh, idUsuario, tipoSeleccionado })
      .pipe(
        tap((refresh: any) => {
          this.usuario.token = refresh.token;
          this._ss.setUsrConfig(this.usuario);
        })
      )
  }
  /////////////////////////////////////////////
  //////////// Api Calls Misc

  async postUsuario(data) {
    const response = await this._http.post(`${URL}/usuario`, data).toPromise();
    return response;
  }
  async updateUsuario(id, data) {
    let params = [];
    const response = await this._http.put(`${URL}/usuario/${id}`, data).toPromise();
    return response;
  }
  async deleteUsuario(id) {
    const response = await this._http.delete(`${URL}/usuario/${id}`).toPromise();
    return response;
  }

  async getGraficasUsuario() {
    let response = <any>await this._http.get(`${URL}/usuario/graficas`).toPromise();
    return response;

  }
}
