import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-drag-files',
  templateUrl: './drag-files.component.html',
  styleUrls: ['./drag-files.component.sass']
})
export class DragFilesComponent implements OnInit {

  extensionImages = ['png','jpeg','jpg','gif','bmp','tiff'];
  @Input()  title: string = '';
  @Input()  titleImage: string = '';
  @Input()  acceptedFiles: string = '.csv, application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, text/plain, .png';
  @Input()  isDisabled: boolean = false;
  @Input()  multiple  : boolean = false;
  @Output() onChangeFiles = new EventEmitter();
  @Input()  previewImages: boolean = false;
  isOpenDrag:boolean = false;
  @Input()  archivos = [];

  constructor(
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit(): void {
  }

  dragOver(event){
    this.isOpenDrag = true;
  }
  dragEnd(event){
    this.isOpenDrag = false;
  }
  dropFiles(files){
    this.isOpenDrag = false;
    if(files){
      this.onFileSelected(null,files);
    }
  }
  getExtension(fileName){
    return fileName.split('.').pop();
  }

  safeUrl(url){
    return this.sanitizer.bypassSecurityTrustResourceUrl(url);
  }

  isImage(file){
    return this.extensionImages.includes(this.getExtension(file.nombreOriginal));
  }

  async onFileSelected($event,files = []) {
    let filesSelected = files.length ? files : $event.target?.files;
    let archivos   = [];

    if(!this.multiple && (filesSelected.length > 1 || this.archivos.length)){
      return
    }

    if(filesSelected.length){
      let lastId = this.archivos[this.archivos?.length - 1]?.id || 0;
      for (let i = 0; i < filesSelected.length; i++) {
        const file: File = filesSelected[i];
        let archivo:any = {
          id: lastId + (i + 1),
          tamano: (file.size / (1024 ** 2)).toFixed(2),
          nombreOriginal: `${file.name}`,
          extension: `${this.getExtension(file.name)}`,
          file
        }
        if(this.previewImages && this.extensionImages.includes(archivo.extension)){
          const fileURL = URL.createObjectURL(file);
          archivo.urlPreview = fileURL;
        }
        archivos.push(archivo);
      }
      this.archivos = [ ...this.archivos,...archivos ];
      this.onChangeFiles.emit({
        accion: 'change',
        archivos: this.archivos
      });
    }
  }

  deletedFile(archivo){
    this.onChangeFiles.emit({
      accion: 'deleted',
      archivos: [ archivo ]
    })
  }

  handleClickFile(archivo){
    if(archivo?.url){
      window.open(archivo.url,'_blank')
    }
  }
}
