import { Component, OnInit, forwardRef, Output, Input, EventEmitter } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { Subscription } from 'rxjs';
import { ConceptoFactura } from 'src/app/shared/classes/concepto-factura.class';
import { Recolecta } from '../../../classes/recolecta.class';
// import { RecolectaFormComponent } from 'src/app/modules/trafico-main/trafico/recolecta-form/recolecta-form.component';
import { MatDialog } from '@angular/material/dialog';
import { DialogService } from 'src/app/services/dialog/dialog.service';
import { HttpError } from '../../../classes/httpError.class';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';


@Component({
  selector: 'app-recolecta-autocomplete',
  templateUrl: './recolecta-autocomplete.component.html',
  styleUrls: ['./recolecta-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RecolectaAutocompleteComponent),
      multi: true
    }
  ]
})
export class RecolectaAutocompleteComponent implements OnInit {

  @Input() label: string = '';
  @Input() modo;
  @Input() set value(value) {
    this._value = value;
  }
  @Output() onSelectedChange = new EventEmitter<Recolecta>();
  appearance: string = '';
  @Input() clearButton = false;
  @Input() idTipoConcepto;
  opciones: Recolecta[];
  recolectas$: Subscription;
  opcionesFiltradas = [];
  _value: Recolecta;
  @Input() isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(concepto: Recolecta): void {
    if (concepto) {
      this.value = concepto;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(e, value: string) {

    this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => opt.locacion.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 10);
  }
  constructor(
    private _cs: CatalogosService, 
    private dialog: MatDialog,
    private _ds: DialogService,
    private _utls: UtilitiesService
  ) {
    this.appearance = this._utls.appearance
  }
  ngOnDestroy(): void {
    if (this.recolectas$) {
      this.recolectas$.unsubscribe();
    }
  }
  handleBlur(e, value: string) {

    let original = this._value?.locacion || "";
    if (value !== original) {
      if (!value) {
        if (this._value) {
          this.seleccionar(null, { blur: true })
        }
      } else {
        let recolecta = this.opcionesFiltradas.find(itm => itm.locacion.toString().toLocaleLowerCase() === value.toString().toLocaleLowerCase());
        if (recolecta) {
          this.seleccionar(recolecta, { blur: true })
        } else {
          if (!this._value) {
            e.target.value = "";
            this.onInput( { blur: true },"")
            if (this._value) {
              this.seleccionar(null, { blur: true })
            }
          } else {
            e.target.value = this._value?.locacion || "";
          }
        }
      }
    }
  }
  ngOnInit() {
    this.inicializar();
  }
  isLoading = false;
  async inicializar() {
    this.recolectas$ = this._cs.lugaresRecolecta$.subscribe(recolectas => {
      
      this.opciones = recolectas;
      
      this.opcionesFiltradas = recolectas;
      this.isLoading = false;
    });
    this.isLoading = true
    await this._cs.loadLugaresRecolectas();


  }
  seleccionar(recolecta: Recolecta, event) {
    if (event.isUserInput || event.blur) {
      if (this._value !== recolecta) {
        this.value = recolecta;
        this.onTouch();
        this.onChange( this._value );
        this.onSelectedChange.emit(recolecta);
      }
    }
  }

  async handleAgregarRecolecta(){
    try { 
      // const resp = await this.dialog.open(RecolectaFormComponent).afterClosed().toPromise();
      // if (resp) {
      //   const res = await this._cs.postLugarRecolecta(resp)
      //   await this._cs.loadLugaresRecolectas();
      //   this._ds.showSnack("Lugar recolecta agregado correctamente")
      // }
    } catch (error) {
      const err = new HttpError(error);
      this._ds.showSnack(err.message || "Error al agregar cliente", null, "error")
    }

  }

  handleClear() {
    this.value = null;
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(null);
  }
  displayFn(option: Recolecta): any {
    return option && option.locacion ? option.locacion : '';
  }
}
