import { Component, Input, OnInit, Output, EventEmitter, forwardRef } from '@angular/core';
import { FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { MatSelectChange } from '@angular/material/select';
import { Subscription } from 'rxjs';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { TipoUsuario } from '../../../interface/TipoUsuario.interface';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
@Component({
  selector: 'app-tipo-usuario-dropdown',
  templateUrl: './tipo-usuario-dropdown.component.html',
  styleUrls: ['./tipo-usuario-dropdown.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoUsuarioDropdownComponent),
      multi: true
    }
  ]
})
export class TipoUsuarioDropdownComponent implements OnInit {

 
  @Input() placeholder: string;
  @Input() appearance;
  value: TipoUsuario;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipo: TipoUsuario): void {
      if (tipo) {
          this.value = tipo;
      } else {
          this.value = null;
      }
  }
  registerOnChange(fn: any): void {
      this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
      this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
      this.isDisabled = isDisabled;
  }

  ngOnDestroy(): void {
      this.usuarioSubs.unsubscribe();
  }

  usuarioSubs: Subscription;
  opciones: TipoUsuario[];
  @Output() onSelectedChange = new EventEmitter();
  constructor(
      private _cs: CatalogosService,
      private _ut:UtilitiesService
  ) { }

  ngOnInit(): void {
      this.appearance = this._ut.appearance;
      this.inicializar();
  }
  async inicializar() {
      this.usuarioSubs = this._cs.tipoUsuarios$.subscribe(data => {

      this.opciones = data;
      if (this.value) {
          this.writeValue(this.value);
      }
      });
      await this._cs.loadTipoUsuarios();
  }

  seleccionar(event: MatSelectChange) {
      let opt = this.opciones.find(item => item.idTipoUsuario === event.value);
      this.value = opt;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(opt);
  }
  resetValue() {
      this.value = this.opciones[0];
  }
}
