import { Component, OnInit, forwardRef, Output, Input, EventEmitter } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { Subscription } from 'rxjs';
import { Moneda } from '../../../classes/moneda.class';


@Component({
  selector: 'app-moneda-autocomplete',
  templateUrl: './moneda-autocomplete.component.html',
  styleUrls: ['./moneda-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => MonedaAutocompleteComponent),
      multi: true
    }
  ]
})
export class MonedaAutocompleteComponent implements OnInit {

  @Input() label: string = '';
  @Input() modo;
  @Input() set value(value) {

    this._value = value;
  }
  @Output() onSelectedChange = new EventEmitter<Moneda>();
  @Input() appearance;
  @Input() clearButton = false;
  opciones: Moneda[];
  opciones$: Subscription;
  opcionesFiltradas = [];
  _value: Moneda;
  @Input() isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(moneda: Moneda): void {
    if (moneda) {
      this.value = moneda;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(e, value: string) {

    this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => opt.idMoneda.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 10);
  }
  constructor(public _cs: CatalogosService) {

  }
  ngOnDestroy(): void {
    if (this.opciones$) {
      this.opciones$.unsubscribe();
    }
  }
  handleBlur(e, value: string) {
    let original = this._value?.idMoneda || "";
    if (value !== original) {
      if (!value) {
        if (this._value) {
          this.seleccionar(null, { blur: true })
        }
      } else {
        let moneda = this.opcionesFiltradas.find(itm => itm.nombre.toString().toLowerCase() === value.toString().toLowerCase());
        if (moneda) {
          this.seleccionar(moneda, { blur: true })
        } else {
          if (!this._value) {
            e.target.value = "";
            this.onInput( { blur: true },"")
            if (this._value) {
              this.seleccionar(null, { blur: true })
            }
          } else {
            e.target.value = this._value?.idMoneda || "";
          }
        }
      }
    }
  }
  ngOnInit() {
    this.inicializar();
  }
  isLoading = false;
  async inicializar() {
    this.opciones$ = this._cs.moneda$.subscribe(monedas => {
      // get por tipo de 
      this.opciones = monedas;
      this.opcionesFiltradas = monedas;
      this.isLoading = false;
    });
    this.isLoading = true
    await this._cs.loadMoneda();

  }
  seleccionar(moneda: Moneda, event) {
    if (event.isUserInput || event.blur) {
      if (this._value !== moneda) {
        this.value = moneda;
        this.onTouch();
        this.onChange(this._value);
        this.onSelectedChange.emit(moneda);
      }
    }
  }
  handleClear() {
    this.value = null;
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(null);
  }
  displayFn(option: Moneda): any {
    return option && option.idMoneda ? option.idMoneda : '';
  }
}
