import { Component, Input, OnInit, Output, EventEmitter, OnDestroy } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-fullscreen-dialog',
  templateUrl: './fullscreen-dialog.component.html',
  styleUrls: ['./fullscreen-dialog.component.sass']
})
export class FullscreenDialogComponent implements OnInit, OnDestroy {

  @Input() title: string;
  @Output() onClose = new EventEmitter();
  keyEvents$: Subscription;
  constructor(public dialogRef: MatDialogRef<FullscreenDialogComponent>,) { }
  ngOnDestroy(): void {
    if (this.keyEvents$) this.keyEvents$.unsubscribe();
  }

  ngOnInit() {
    this.keyEvents$ = this.dialogRef.keydownEvents().subscribe(event => {
      if (event.key === "Escape") {
        this.close();
      }
    });

    // this.dialogRef.backdropClick().subscribe(event => {
    //     this.close();
    // });
  }
  close() {
    this.onClose.emit();
    this.dialogRef.close();
  }
}
