import { Component, forwardRef, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subscription } from 'rxjs';
import { CatalogosService } from 'src/app/services/catalogos/catalogos.service';
import { TipoBulto } from 'src/app/shared/classes/tipo-bulto.class';


@Component({
  selector: 'app-tipo-bulto',
  templateUrl: './tipo-bulto.component.html',
  styleUrls: ['./tipo-bulto.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoBultoComponent),
      multi: true
    }
  ]
})
export class TipoBultoComponent implements OnInit {

  @Output() onSelectedChange = new EventEmitter<TipoBulto>();
  @Input() appearance;
  @Input() clearButton = false;
  @Input() label = "Tipo Bulto";
  @Input() aduanas = null;
  @Input() interno = undefined;
  opciones$: Subscription;
  opciones: TipoBulto[];
  opcionesFiltradas = [];
  value: TipoBulto;
  @Input() error;
  @Input() isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipoBulto: TipoBulto): void {
    if (tipoBulto) {
      this.value = tipoBulto;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(e, value: string) {

    this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => opt.nombre.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 10);
  }
  constructor(public _cs: CatalogosService) {

  }
  ngOnDestroy(): void {
    if (this.opciones$) {
      this.opciones$.unsubscribe();
    }
  }

  ngOnInit() {
    this.inicializar();
  }
  isLoading = false;
  async inicializar() {
    this.isLoading = true
    this.opciones$ = this._cs.tipoBulto$.subscribe(tipoDocs => {
      this.isLoading = true
      this.opciones = tipoDocs;
      this.opcionesFiltradas = tipoDocs;

      this.isLoading = false;
    });
    await this._cs.loadTiposBulto();

  }
  seleccionar(tipoBulto: TipoBulto, event) {
    if (event.isUserInput) {
      this.value = tipoBulto;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(tipoBulto);
    }
  }




  handleClear() {
    this.value = null;
    //this.text = banco.nombre;
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(null);
  }
  displayFn(option: TipoBulto): any {
    return option && option.nombre ? option.nombre : '';
  }

}
