import { Component, OnInit, forwardRef, EventEmitter, Input, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR, FormControl } from '@angular/forms';
import { Subscription, debounceTime } from 'rxjs';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { filter } from 'rxjs/operators';
import { EntidadSeccion } from '../../../interface/EntidadSeccion';

@Component({
  selector: 'app-entidad-seccion-autocomplete',
  templateUrl: './entidad-seccion-autocomplete.component.html',
  styleUrls: ['./entidad-seccion-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => EntidadSeccionAutocompleteComponent),
      multi: true
    }
  ]
})
export class EntidadSeccionAutocompleteComponent implements OnInit {

  @Input() placeholder: string;
  appearance: string = '';
  @Output() onSelectedChange = new EventEmitter();
  @Input() set idEntidad(value) {
    if (this._idEntidad !== value) {
      this.opcionesFiltradas = [];
      this.clearValue();
    } else {
    }
    this._idEntidad = value
    this.opcionesFiltradas = this.opciones.filter(itm => itm.idEntidad === this._idEntidad).slice(0, 30);

  };
  _idEntidad;
  opciones$: Subscription;
  opciones = [];
  opcionesFiltradas = [];
  text = new FormControl()
  value;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(value): void {
    if (value) {
      this.seleccionar(value, { isUserInput: true })
    }
    else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(value: string) {
    // if (typeof (value) === 'string') {
    //   let filters = <any>[
    //     { "filters[nombre][like]": `%${value}%` },
    //     { 'filters[$Entidad.idTipoEntidad$][eq]': this.idTipoEntidad },
    //   ]
    //   if (this._idEntidad) filters.push({ "filters[idEntidad][eq]": this._idEntidad },)
    //   this.opcionesFiltradas = <any>await this._cs.getCatalogo('entidadSeccion', filters, "nombre", 10, 0);
    // }
    if (typeof (value) === 'string') {
      if (this.idEntidad > 0) {
        this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => this._idEntidad === opt.idEntidad && opt.nombre.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 30);
      } else {
        this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => opt.nombre.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 30);
      }
    }
  }
  constructor(
    public _cs: CatalogosService,
    private _utls: UtilitiesService
  ) {
    this.appearance = this._utls.appearance;
  }
  ngOnDestroy(): void {
    if (this.opciones$) this.opciones$.unsubscribe()
  }

  ngOnInit() {
    this.opciones$ = this.text.valueChanges.pipe(
      filter((value: any) => {
        if (typeof (value) === 'object') return false;
        return value !== null;
      })
    ).subscribe(text => this.onInput(text))
    this.inicializar();
  }
  async inicializar() {
    this.opciones$ = this._cs.secciones$.subscribe(data => {
      if (this._idEntidad) {
        this.opciones = data// .filter(itm => itm.idEntidad === this._idEntidad);
        this.opcionesFiltradas = data.filter(itm => itm.idEntidad === this._idEntidad).slice(0, 30);
      } else {
        this.opciones = data
        this.opcionesFiltradas = data.slice(0, 30);
      }
      if (this.value) {
        this.writeValue(this.value);
      }
    });
    await this._cs.loadSecciones();
  }

  seleccionar(entidad: EntidadSeccion, event) {
    if (event.isUserInput) {
      this.value = entidad;
      this.text.setValue(entidad, { emitEvent: false })
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(entidad);
    }
  }
  clearValue() {
    this.value = null;
    this.text.setValue("", { emitEvent: false })
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(this.value);

  }
  handleBlur() {
    if (!this.value || !this.value.value) {
      this.clearValue();
    } else {
      let { text } = this.value;
      let { nombre } = this.value.value;
      if (text !== nombre) {
        this.text.setValue(nombre, { emitEvent: false })
      }
    }
  }
  displayFn(option): any {
    return option && option.nombre
      ? `${option.nombre}`
      : '';
  }
}
