import { Component, forwardRef, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subscription } from 'rxjs';
import { CatalogosService } from 'src/app/services/catalogos/catalogos.service';
import { TipoEntrada } from '../../../classes/tipo-entrada.class';


@Component({
  selector: 'app-tipo-entrada-autocomplete',
  templateUrl: './tipo-entrada-autocomplete.component.html',
  styleUrls: ['./tipo-entrada-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoEntradaAutocompleteComponent),
      multi: true
    }
  ]
})
export class TipoEntradaAutocompleteComponent implements OnInit {
  @Output() onSelectedChange = new EventEmitter<TipoEntrada>();
  @Input() appearance;
  @Input() clearButton = false;
  @Input() label = "Tipo Entrada";
  @Input() aduanas = null;
  @Input() interno = undefined;
  opciones$: Subscription;
  opciones: TipoEntrada[];
  opcionesFiltradas = [];
  value: TipoEntrada;
  @Input() error;
  @Input() isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipoEntrada: TipoEntrada): void {
    if (tipoEntrada) {
      this.value = tipoEntrada;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(e, value: string) {

    this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => opt.nombre.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 10);
  }
  constructor(public _cs: CatalogosService) {

  }
  ngOnDestroy(): void {
    if (this.opciones$) {
      this.opciones$.unsubscribe();
    }
  }

  ngOnInit() {
    this.inicializar();
  }
  isLoading = false;
  async inicializar() {
    this.isLoading = true
    this.opciones$ = this._cs.tipoEntrada$.subscribe(tipoDocs => {
      this.isLoading = true
      this.opciones = tipoDocs;
      this.opcionesFiltradas = tipoDocs;

      this.isLoading = false;
    });
    await this._cs.loadTiposEntrada();

  }
  seleccionar(tipoEntrada: TipoEntrada, event) {
    if (event.isUserInput) {
      this.value = tipoEntrada;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(tipoEntrada);
    }
  }




  handleClear() {
    this.value = null;
    //this.text = banco.nombre;
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(null);
  }
  displayFn(option: TipoEntrada): any {
    return option && option.nombre ? option.nombre : '';
  }


}
