import { Component, OnInit, forwardRef, Input, EventEmitter, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR, FormControl } from '@angular/forms';
import { Subscription, defaultIfEmpty, debounceTime } from 'rxjs';
import { Entidad } from '../../../interface/Entidad.interface';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { filter } from 'rxjs/operators';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { DialogService } from 'src/app/services/dialog/dialog-v2.service';
// import { EntidadFormComponent } from '../../../modules/catalogo/entidad-reporte/entidad-form/entidad-form.component';
import { EntidadService } from '../../../../services/entidad/entidad.service';


@Component({
  selector: 'app-entidad-autocomplete',
  templateUrl: './entidad-autocomplete.component.html',
  styleUrls: ['./entidad-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => EntidadAutocompleteComponent),
      multi: true
    }
  ]
})
export class EntidadAutocompleteComponent implements OnInit {

  @Input() modo;
  @Input() placeholder: string;
  @Input() entidadLabel: string = '';
  appearance: string = '';
  @Output() onSelectedChange = new EventEmitter();
  @Input() idTipoEntidad: number[] = [];
  opciones$: Subscription;
  opciones = [];
  opcionesFiltradas = [];
  text = new FormControl()
  _value;
  @Input() set value(value) {
    this._value = value;
  }
  @Input() isDisabled: boolean;

  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(entidad): void {
    if (entidad) {
      this.seleccionar(entidad, { isUserInput: true })
    }
    else {
      this._value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(value: string) {
    if (typeof (value) === 'string') {
      if (this.idTipoEntidad.length > 0) {
        this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => this.idTipoEntidad.includes(opt.idTipoEntidad) && opt.razonSocial.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 30);
      } else {
        this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => opt.razonSocial.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 30);
      }
    }
  }
  constructor(
    private _es: EntidadService,
    public _cs: CatalogosService,
    private _utls: UtilitiesService,
    private _ds: DialogService
  ) {
    this.appearance = this._utls.appearance;
  }
  ngOnDestroy(): void {
    if (this.opciones$) this.opciones$.unsubscribe()
  }


  ngOnInit() {
    // if (this.idTipoEntidad) {
    //   this.tipoCatEntidad = this._es.getTipoEntidad(this.idTipoEntidad);
    // }
    // this.nombreAgregar = `Agregar ${this.nombreAgregar || this.tipoCatEntidad}`

    this.opciones$ = this.text.valueChanges.pipe(
      filter((value: any) => {
        if (typeof (value) === 'object') return false;
        return value !== null;
      })
    ).subscribe(text => this.onInput(text))

    this.inicializar()
  }
  async inicializar() {
    this.opciones$ = this._cs.entidad$.subscribe(data => {
      if (this.idTipoEntidad.length > 0) {
        this.opciones = data.filter(opt => this.idTipoEntidad.includes(opt.idTipoEntidad))
        this.opcionesFiltradas = data.filter(opt => this.idTipoEntidad.includes(opt.idTipoEntidad)).slice(0, 30);
      } else {
        this.opciones = data
        this.opcionesFiltradas = data.slice(0, 30);
      }
      if (this._value) {
        this.writeValue(this._value);
      }
    });
    await this._cs.loadEntidad();
  }
  seleccionar(entidad: Entidad, event) {
    if (event.isUserInput) {
      this._value = entidad;
      this.text.setValue(entidad, { emitEvent: false })
      this.onTouch();
      this.onChange(this._value);
      this.onSelectedChange.emit(entidad);
    }
  }
  clearValue() {
    this._value = null;
    this.text.setValue("", { emitEvent: false })
    this.onTouch();
    this.onChange(this._value);
    this.onSelectedChange.emit(this._value);
    this.opcionesFiltradas = [...this.opciones];
  }

  async handleAddEntidad() {

    try {
      // const nuevaEntidad = await this._ds.openModalDialogComponent(EntidadFormComponent, { entidadLabel: this.entidadLabel, idTipoEntidad: this.idTipoEntidad });

      // if (nuevaEntidad) {
      //   this.opciones.push(nuevaEntidad);
      //   this.opcionesFiltradas.push(nuevaEntidad)
      //   this._cs.catalogos.entidades.push(nuevaEntidad);
      //   this.seleccionar(nuevaEntidad, { isUserInput: true });
      // }
    } catch (error) {
      console.log(error)
    }

  }

  handleBlur(e, value: string) {
    let original = this._value?.idEntidad || "";
    if (value !== original) {
      if (!value) {
        if (this._value) {
          this.seleccionar(null, { blur: true })
        }
      } else {
        let entidad = this.opcionesFiltradas.find(itm => itm.razonSocial.toString().toLowerCase() === value.toString().toLowerCase());
        if (entidad) {
          this.seleccionar(entidad, { blur: true })
        } else {
          if (!this._value) {
            //e.target.value = "";
            //this.onInput("")
            // if (this._value) {
            //   this.seleccionar(null, { blur: true })
            // }
          } else {
            e.target.value = this._value?.razonSocial || "";
          }
        }
      }
    }
  }

  displayFn(option): any {
    return option && option.idEntidad ? option.razonSocial : '';
  }

}
