import { Component, OnInit, forwardRef, Input, EventEmitter, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { TipoImpuesto } from '../../../classes/tipo-impuesto.class';
import { Subscription } from 'rxjs';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';

@Component({
  selector: 'app-tipo-impuesto-autocomplete',
  templateUrl: './tipo-impuesto-autocomplete.component.html',
  styleUrls: ['./tipo-impuesto-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoImpuestoAutocompleteComponent),
      multi: true
    }
  ]
})
export class TipoImpuestoAutocompleteComponent implements OnInit {

  @Input() modo;
  _idUnidadNegocio;
  @Input() set value(value) {
    this._value = value;
  }
  @Output() onSelectedChange = new EventEmitter<TipoImpuesto>();
  @Input() appearance;
  @Input() clearButton = false;
  @Input() idTipoConcepto;
  opciones: TipoImpuesto[];
  opciones$: Subscription;
  opcionesFiltradas = [];
  _value: TipoImpuesto;
  @Input() isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipoImpuesto: TipoImpuesto): void {
    if (tipoImpuesto) {
      this.value = tipoImpuesto;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(e, value: string) {

    this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => opt.descripcion.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 10);
  }
  constructor(public _cs: CatalogosService) {

  }
  ngOnDestroy(): void {
    if (this.opciones$) {
      this.opciones$.unsubscribe();
    }
  }
  handleBlur(e, value: string) {

    let original = this._value?.descripcion || "";
    if (value !== original) {
      if (!value) {
        if (this._value) {
          this.seleccionar(null, { blur: true })
        }
      } else {
        let concepto = this.opcionesFiltradas.find(itm => itm.descripcion.toString().toLocaleLowerCase() === value.toString().toLocaleLowerCase());
        if (concepto) {
          this.seleccionar(concepto, { blur: true })
        } else {
          if (!this._value) {
            // e.target.value = "";
            // this.onInput( { blur: true },"")
            // if (this._value) {
            //   this.seleccionar(null, { blur: true })
            // }
          } else {
            e.target.value = this._value?.descripcion || "";
          }
        }
      }
    }
  }
  ngOnInit() {
    this.inicializar();
  }
  isLoading = false;
  async inicializar() {
    this.opciones$ = this._cs.tipoImpuestos$.subscribe(tipos => {
      this.opciones = tipos;
      this.isLoading = false;
    });
    this.isLoading = true
    await this._cs.loadTipoImpuesto();
  }
  seleccionar(tipoImpuesto: TipoImpuesto, event) {
    if (event.isUserInput || event.blur) {
      this._value = tipoImpuesto;
      this.onTouch();
      this.onChange(this._value);
      this.onSelectedChange.emit(tipoImpuesto);
    }
  }
  handleClear() {
    this._value = null;
    this.onTouch();
    this.onChange(this._value);
    this.onSelectedChange.emit(null);
  }
  displayFn(option: TipoImpuesto): any {
    return option && option.descripcion ? option.descripcion : '';
  }

}
