import { Component, OnInit, forwardRef, Output, Input, EventEmitter } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { Subscription } from 'rxjs';
import { ConceptoFactura } from 'src/app/shared/classes/concepto-factura.class';
import { Entrega } from '../../../classes/entrega.class';
// import { EntregaFormComponent } from 'src/app/modules/trafico-main/trafico/entrega-form/entrega-form.component';
import { MatDialog } from '@angular/material/dialog';
import { DialogService } from 'src/app/services/dialog/dialog.service';
import { HttpError } from '../../../classes/httpError.class';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';


@Component({
  selector: 'app-entrega-autocomplete',
  templateUrl: './entrega-autocomplete.component.html',
  styleUrls: ['./entrega-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => EntregaAutocompleteComponent),
      multi: true
    }
  ]
})
export class EntregaAutocompleteComponent implements OnInit {

  @Input() label: string = '';
  @Input() modo;
  @Input() set value(value) {

    this._value = value;
  }
  @Output() onSelectedChange = new EventEmitter<Entrega>();
  appearance: string = '';
  @Input() clearButton = false;
  @Input() idTipoConcepto;
  opciones: Entrega[];
  entregas$: Subscription;
  opcionesFiltradas = [];
  _value: Entrega;
  @Input() isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(concepto: Entrega): void {
    if (concepto) {
      this.value = concepto;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(e, value: string) {

    this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => opt.locacion.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 10);
  }
  constructor(
    public _cs: CatalogosService, 
    private dialog: MatDialog,
    private _ds: DialogService,
    private _utls: UtilitiesService
  ) {
    this.appearance = this._utls.appearance
  }
  ngOnDestroy(): void {
    if (this.entregas$) {
      this.entregas$.unsubscribe();
    }
  }
  handleBlur(e, value: string) {

    let original = this._value?.locacion || "";
    if (value !== original) {
      if (!value) {
        if (this._value) {
          this.seleccionar(null, { blur: true })
        }
      } else {
        let entrega = this.opcionesFiltradas.find(itm => itm.locacion.toString().toLocaleLowerCase() === value.toString().toLocaleLowerCase());
        if (entrega) {
          this.seleccionar(entrega, { blur: true })
        } else {
          if (!this._value) {
            e.target.value = "";
            this.onInput( { blur: true },"")
            if (this._value) {
              this.seleccionar(null, { blur: true })
            }
          } else {
            e.target.value = this._value?.locacion || "";
          }
        }
      }
    }
  }
  ngOnInit() {
    this.inicializar();
  }
  isLoading = false;
  async inicializar() {
    this.entregas$ = this._cs.lugaresEntrega$.subscribe(entregas => {
      
      this.opciones = entregas;
      
      this.opcionesFiltradas = entregas;
      this.isLoading = false;
    });
    this.isLoading = true
    await this._cs.loadLugaresEntrega();


  }
  seleccionar(entrega: Entrega, event) {
    if (event.isUserInput || event.blur) {
      if (this._value !== entrega) {
        this.value = entrega;
        this.onTouch();
        this.onChange( this._value );
        this.onSelectedChange.emit(entrega);
      }
    }
  }

  async handleAgregarEntrega(){
    try { 
      // const resp = await this.dialog.open(EntregaFormComponent).afterClosed().toPromise();
      // if (resp) {
      //   const res = await this._cs.postLugarEntrega(resp)
      //   await this._cs.loadLugaresEntrega();
      //   this._ds.showSnack("Lugar entrega agregado correctamente")
      // }
    } catch (error) {
      const err = new HttpError(error);
      this._ds.showSnack(err.message || "Error al agregar cliente", null, "error")
    }

  }

  handleClear() {
    this.value = null;
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(null);
  }
  displayFn(option: Entrega): any {
    return option && option.locacion ? option.locacion : '';
  }
}
