
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { URL } from 'src/app/config/url.services';
import { Subject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class EntidadService {

    constructor(private _http: HttpClient) { }

    getTipoEntidad(idTipoEntidad) {
        if ([1].includes(idTipoEntidad)) return 'cliente'
        if ([2, 3, 4, 5, 6, 7, 8, 10, 11].includes(idTipoEntidad)) return 'socio'
        if ([9].includes(idTipoEntidad)) return 'almacen'
    }

    async getEntidad(idEntidad, filters = null, sort = null, size = null, page = null) {
        let params = [];
        if (filters) params = filters.map(item => `${Object.keys(item)[0]}=${Object.values(item)[0]}`)
        if (sort) params.push("sort=" + sort);
        if (size) params.push("size=" + size);
        if (page) params.push("page=" + page);
        const queryStr = params.join("&");
        const response = await this._http.get(`${URL}/entidad/${idEntidad}${queryStr ? "?" + queryStr : ""}`).toPromise();
        return response;
    }

    async postEntidad(data) {
        const response = await this._http.post(`${URL}/entidad`, data).toPromise();
        return response;
    }
    async updateEntidad(id, data) {
        let params = [];
        const response = await this._http.put(`${URL}/entidad/${id}`, data).toPromise();
        return response;
    }
    async deleteEntidad(id) {
        const response = await this._http.delete(`${URL}/entidad/${id}`).toPromise();
        return response;
    }
    async postEntidadContacto(idEntidad, data) {
        const response = await this._http.post(`${URL}/entidad/${idEntidad}/contacto`, data).toPromise();
        return response;
    }
    async updateEntidadContacto(idEntidad, idEntidadContacto, data) {
        let params = [];
        const response = await this._http.put(`${URL}/entidad/${idEntidad}/contacto/${idEntidadContacto}`, data).toPromise();
        return response;
    }
    async deleteEntidadContacto(idEntidad, idEntidadContacto) {
        const response = await this._http.delete(`${URL}/entidad/${idEntidad}/contacto/${idEntidadContacto}`).toPromise();
        return response;
    }
    async postEntidadDireccion(idEntidad, data) {
        const response = await this._http.post(`${URL}/entidad/${idEntidad}/direccion`, data).toPromise();
        return response;
    }
    async updateEntidadDireccion(idEntidad, idEntidadDireccion, data) {
        let params = [];
        const response = await this._http.put(`${URL}/entidad/${idEntidad}/direccion/${idEntidadDireccion}`, data).toPromise();
        return response;
    }
    async deleteEntidadDireccion(idEntidad, idEntidadDireccion) {
        const response = await this._http.delete(`${URL}/entidad/${idEntidad}/direccion/${idEntidadDireccion}`).toPromise();
        return response;
    }
    async postEntidadSeccion(idEntidad, data) {
        const response = await this._http.post(`${URL}/entidad/${idEntidad}/seccion`, data).toPromise();
        return response;
    }
    async updateEntidadSeccion(idEntidad, idEntidadSeccion, data) {
        let params = [];
        const response = await this._http.put(`${URL}/entidad/${idEntidad}/seccion/${idEntidadSeccion}`, data).toPromise();
        return response;
    }
    async deleteEntidadSeccion(idEntidad, idEntidadSeccion) {
        const response = await this._http.delete(`${URL}/entidad/${idEntidad}/seccion/${idEntidadSeccion}`).toPromise();
        return response;
    }
}