import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { URL } from '../../config/url.services';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class UtilitiesService {

  private _appearance:string = 'outline';

  get appearance():string{
    let apperance = this._appearance;
    return apperance;
  }

  constructor(private _http: HttpClient) { }
  uniqueBy(arr, prop) {
    return arr.reduce((a, d) => {
      if (!a.find(itm => itm[prop] === d[prop])) { a.push(d); }
      return a;
    }, []);

  }
  async toExcel(data: any) {
    const response = await this._http.post(`${URL}/utilerias/jsonToExcel`, { data }, { responseType: 'arraybuffer' }).toPromise();
    return response;
  }

  extractPedRegExp(string: string): any[] {
    // let rx = /([0-9]{3})\-([0-9]{4})\-([0-9]{7})/g // Expresion regular que regresa solo pedimentos 
    let rxGen = /([0-9]{3})\-([0-9]{4})\-([0-9]{7})\/?([0-9]*)?/g //Expresion regular que regresa pedimentos y remesas
    return string.match(rxGen);
  }

  isValidDate(fecha) {
    const regex = /(0[1-9]|[12][0-9]|3[01])[\/.-](0[1-9]|1[012])[\/.-]\d{4}$/;
    const regexH = /(0[1-9]|[12][0-9]|3[01])[\/.-](0[1-9]|1[012])[\/.-]\d{4} (2[0-3]|[01][0-9]):[0-5][0-9]$/;
    if (fecha.match(regex) || fecha.match(regexH)) {
      return true;
    }
    return false;
  }
  convertToDate(fecha) {

    try {
      if (!fecha) return null;
      let dia = fecha.substring(0, 2);
      let mes = fecha.substring(3, 5);
      let anio = fecha.substring(6, 10);
      let hora = fecha.substring(11, 13) || "00";
      let minuto = fecha.substring(14, 16) || "00"
      return `${anio}-${mes}-${dia} ${hora}:${minuto}`
    } catch (error) {
      return null;
    }
  }
  convertDateObjectToDate( date , defaultHour = false ){
    let dateObject = date.toObject();
    return `${dateObject.years}-${(dateObject.months + 1).toString().padStart(2, "0")}-${dateObject.date.toString().padStart(2, "0")}${defaultHour ? ' 23:59:59' : ''}`
  }
}
