import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-file-picker',
  templateUrl: './file-picker.component.html',
  styleUrls: ['./file-picker.component.sass']
})
export class FilePickerComponent implements OnInit {
  _selectedFiles: any[] = [];
  @Input() accept: string;
  @Input() cleanFiles = new EventEmitter();
  @Output() selectedFiles: EventEmitter<any>;

  cleanFiles$:Subscription;
  constructor() {
    this.selectedFiles = new EventEmitter();
  }

  isOpenDrag: boolean = false

  dragOver(event) {
    this.isOpenDrag = true;
  }

  dragEnd(event) {
    this.isOpenDrag = false;
  }

  dropFiles(files) {
    this.isOpenDrag = false;
    if (files) {
      this.onFileSelected(null,files)
    }
  }
  handleDeleteFile( file ){
    this._selectedFiles = [];
  }

  ngOnInit(): void {
    this.cleanFiles$ = this.cleanFiles.subscribe(clean=>{
      this._selectedFiles = [];
    })
  }
  onFileSelected($event , files = []) {
    let file = $event ? $event.target.files[0] : files[0];
    this._selectedFiles = [file];
    this.selectedFiles.emit(this._selectedFiles);
  }
}
