import { Component, OnInit, forwardRef, EventEmitter, Output, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { MatSelectChange } from '@angular/material/select';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-tipo-cotizacion-dropdown',
  templateUrl: './tipo-cotizacion-dropdown.component.html',
  styleUrls: ['./tipo-cotizacion-dropdown.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoCotizacionDropdownComponent),
      multi: true
    }
  ]
})
export class TipoCotizacionDropdownComponent implements OnInit {

  tiposCotizaciones: any[] = [];
  @Input() placeholder: string;
  appearance;
  value: any;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipo: any): void {
    if (tipo) {
      this.value = tipo;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  ngOnDestroy(): void {
    this.tipoCotizaciones$.unsubscribe();
  }

  tipoCotizaciones$: Subscription;

  opciones: any[];
  @Output() onSelectedChange = new EventEmitter();

  constructor(
    private _cs: CatalogosService,
    private _ut: UtilitiesService
  ) { }

  ngOnInit(): void {
    this.appearance = this._ut.appearance;
    this.inicializar();
  }
  async inicializar() {
    this.tipoCotizaciones$ = this._cs.tipoCotizaciones$.subscribe(tipos => {
      this.tiposCotizaciones = tipos;
      this.opciones = tipos
      if (this.value) {
        this.writeValue(this.value);
      }
    });
    await this._cs.loadTipoCotizacion();
  }

  seleccionar(event: MatSelectChange) {
    let opt = this.opciones.find(item => item.idTipoCotizacion === event.value);
    this.value = opt;
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(opt);
  }
  resetValue() {
    this.value = this.opciones[0];
  }
}
