import { NgxMatDateFormats, NgxMatNativeDateAdapter } from "@angular-material-components/datetime-picker";
import { MatDateFormats } from "@angular/material/core";
import { Injectable } from "@angular/core";
//import { NativeDateAdapter } from '@angular/material/core';


@Injectable()
export class AppDateAdapter extends NgxMatNativeDateAdapter {
    format(date: Date, displayFormat: Object): string {
        if (displayFormat === "input") {
            let day: string = date.getDate().toString();
            day = +day < 10 ? "0" + day : day;
            let month: string = (date.getMonth() + 1).toString();
            month = +month < 10 ? "0" + month : month;
            let year = date.getFullYear();
            let hour: string = date.getHours().toString();
            hour = +hour < 10 ? "0" + hour : hour;
            let minute: string = date.getMinutes().toString();
            minute = +minute < 10 ? "0" + minute : minute;
            return `${day}-${month}-${year} ${hour}:${minute}`;
        }
        return date.toDateString();
    }
}
export const APP_DATE_FORMATS: MatDateFormats = {
    parse: {
        dateInput: { day: "numeric", month: "short", year: "numeric", hour: '2-digit', minute: '2-digit', hourCycle: 'h23', },
    },
    display: {
        dateInput: "input",
        monthYearLabel: { year: "numeric", month: "numeric" },
        dateA11yLabel: {
            year: "numeric", month: "long", day: "numeric"
        },
        monthYearA11yLabel: { year: "numeric", month: "long" },
    }
};

export const CUSTOM_DATE_FORMATS: NgxMatDateFormats = {
    parse: {
        dateInput: "DD-MM-YYYY HH:mm"
    },
    display: {
        dateInput: "DD-MM-YYYY HH:mm",
        monthYearLabel: "MMM YYYY",
        dateA11yLabel: "LL",
        monthYearA11yLabel: "MMMM YYYY"
    }
};
