import { Component, forwardRef, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subscription } from 'rxjs';
import { CatalogosService } from 'src/app/services/catalogos/catalogos.service';
import { Banco } from '../../../classes/banco.class';

@Component({
  selector: 'app-banco-autocomplete',
  templateUrl: './banco-autocomplete.component.html',
  styleUrls: ['./banco-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => BancoAutocompleteComponent),
      multi: true
    }
  ]
})
export class BancoAutocompleteComponent implements OnInit {

  @Output() onSelectedChange = new EventEmitter<Banco>();
  @Input() appearance;
  @Input() clearButton = false;
  @Input() label = "Banco";
  @Input() aduanas = null;
  bancos$: Subscription;
  opciones: Banco[];
  opcionesSubs: Subscription;
  opcionesFiltradas = [];
  value: Banco;
  @Input() isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(banco: Banco): void {
    if (banco) {
      this.value = banco;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(e, value: string) {

    this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => opt.nombre.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 10);
  }
  constructor(public _cs: CatalogosService) {

  }
  ngOnDestroy(): void {
    if (this.bancos$) {
      this.bancos$.unsubscribe();
    }
  }

  ngOnInit() {
    this.inicializar();
  }
  isLoading = false;
  async inicializar() {
    this.isLoading = true
    this.bancos$ = this._cs.bancos$.subscribe(bancos => {
      this.isLoading = true
      
      this.opciones = bancos;

      this.opcionesFiltradas = bancos;
      this.isLoading = false;
    });
    await this._cs.loadBancos();

  }
  seleccionar(banco: Banco, event) {
    if (event.isUserInput) {
      this.value = banco;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(banco);
    }
  }

 

  
  handleClear() {
    this.value = null;
    //this.text = banco.nombre;
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(null);
  }
  displayFn(option: Banco): any {
    return option && option.nombre ? option.nombre : '';
  }
}
