import { Component, forwardRef, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { Subscription } from 'rxjs';
import { CatalogosService } from 'src/app/services/catalogos/catalogos.service';
import { TipoDocumento } from 'src/app/shared/classes/tipo-documento.class';


@Component({
  selector: 'app-tipo-documento-autocomplete',
  templateUrl: './tipo-documento-autocomplete.component.html',
  styleUrls: ['./tipo-documento-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoDocumentoAutocompleteComponent),
      multi: true
    }
  ]
})
export class TipoDocumentoAutocompleteComponent implements OnInit {

  @Output() onSelectedChange = new EventEmitter<TipoDocumento>();
  @Input() appearance;
  @Input() clearButton = false;
  @Input() label = "Tipo Documento";
  @Input() aduanas = null;
  @Input() interno = undefined;
  opciones$: Subscription;
  opciones: TipoDocumento[];
  opcionesSubs: Subscription;
  opcionesFiltradas = [];
  value: TipoDocumento;
  @Input() error;
  @Input() isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipoDocumento: TipoDocumento): void {
    if (tipoDocumento) {
      this.value = tipoDocumento;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(e, value: string) {

    this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => opt.nombre.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 10);
  }
  constructor(public _cs: CatalogosService) {

  }
  ngOnDestroy(): void {
    if (this.opciones$) {
      this.opciones$.unsubscribe();
    }
  }

  ngOnInit() {
    this.inicializar();
  }
  isLoading = false;
  async inicializar() {
    this.isLoading = true
    this.opciones$ = this._cs.tipoDocumento$.subscribe(tipoDocs => {
      this.isLoading = true

      if (this.interno !== undefined) {
        this.opciones = tipoDocs.filter(itm => itm.interno === this.interno);
        this.opcionesFiltradas = tipoDocs.filter(itm => itm.interno === this.interno);

      } else {
        this.opciones = tipoDocs;
        this.opcionesFiltradas = tipoDocs;

      }

      this.isLoading = false;
    });
    await this._cs.loadTiposDocumento();

  }
  seleccionar(tipoDocumento: TipoDocumento, event) {
    if (event.isUserInput) {
      this.value = tipoDocumento;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(tipoDocumento);
    }
  }




  handleClear() {
    this.value = null;
    //this.text = banco.nombre;
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(null);
  }
  displayFn(option: TipoDocumento): any {
    return option && option.nombre ? option.nombre : '';
  }

}
