import { Component, OnInit, EventEmitter, Output, Input, forwardRef } from '@angular/core';
import { debounceTime, Subscription } from 'rxjs';
import { NG_VALUE_ACCESSOR, FormControl } from '@angular/forms';
import { CATMunicipio } from '../../../interface/CATMunicipio.interface';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';

@Component({
  selector: 'app-municipio-autocomplete',
  templateUrl: './municipio-autocomplete.component.html',
  styleUrls: ['./municipio-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => MunicipioAutocompleteComponent),
      multi: true
    }
  ]
})
export class MunicipioAutocompleteComponent implements OnInit {

  municipioCtrl: FormControl = new FormControl();
  @Output() onSelectedChange = new EventEmitter<CATMunicipio>();
  @Input() placeholder: string
  appearance;
  @Input() clearButton = false;
  estado$: Subscription;
  opciones: CATMunicipio[] = []
  opcionesFiltradas: CATMunicipio[] = []
  value: CATMunicipio;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(pais: CATMunicipio): void {
    if (pais) {
      this.municipioCtrl.patchValue(pais);
    } else {
      this.municipioCtrl.patchValue(null);
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(value: string) {
    if (typeof (value) === 'string') {
      this.opcionesFiltradas = <any[]>await this._cs.getCatalogo('municipios',
        [{ 'filters[nombre][like]': `%25${value}%25` }],
        'nombre,asc',
        10,
        0
      );
    }
  }
  constructor(
    private _cs: CatalogosService,
    private _utls: UtilitiesService,
  ) {

  }
  ngOnDestroy(): void {
    if (this.subs) {
      this.subs.unsubscribe();
    }
  }

  subs: Subscription;
  ngOnInit() {
    this.inicializar();
    this.appearance = this._utls.appearance;

    this.subs = this.municipioCtrl.valueChanges.pipe(
      debounceTime(400)
    ).subscribe(text => this.onInput(text))
  }
  isLoading = false;
  async inicializar() {
    // this.estado$ = this._cs.municipios$.subscribe(municipios => {
    //   // get por tipo de 
    //   this.opciones = municipios;
    //   this.opcionesFiltradas = municipios;
    //   this.isLoading = false;
    // });
    // this.isLoading = true
    // await this._cs.loadMunicipio();
  }
  seleccionar(pais: CATMunicipio, event) {
    if (event.isUserInput) {
      this.value = pais;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(pais);
    }
  }
  handleClear() {
    this.municipioCtrl.patchValue(null);
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(null);
  }
  displayFn(option: CATMunicipio): any {
    return option && option.nombre
      ? `${option.nombre}, ${option.CATEstado.nombre}, ${option.CATEstado.Pais.nombre}`
      : '';
  }

}
