import { Component, Inject, OnInit, Optional } from '@angular/core';
import { ModalDialogData } from '../../../../classes/confirmDialogData';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-modal-option-picker',
  templateUrl: './modal-option-picker.component.html',
  styleUrls: ['./modal-option-picker.component.sass']
})
export class ModalOptionPickerComponent implements OnInit {

  modalDialogData: ModalDialogData;
  constructor(
    public dialogRef: MatDialogRef<ModalOptionPickerComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data
  ) {
    this.modalDialogData = data.modalDialogData;
    if (this.modalDialogData?.opciones?.length > 0) {
      this.opcion.setValue(this.modalDialogData.opciones[0])
    }
  }
  ngOnInit() { }
  opcion = new FormControl(null, Validators.required)
  handleAceptarClick() {
    this.dialogRef.close(this.opcion.value);
  }

}
