import { Component, OnInit, ViewChild, ElementRef, forwardRef, Input, Output, EventEmitter } from '@angular/core';
import { UtilitiesService } from '../../../../../services/utilities/utilities.service';
import { FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';
import { debounceTime, delay, Subject, Subscription } from 'rxjs';

@Component({
  selector: 'app-input-currency',
  templateUrl: './input-currency.component.html',
  styleUrls: ['./input-currency.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => InputCurrencyComponent),
      multi: true
    }
  ]
})
export class InputCurrencyComponent implements OnInit {

  original = '';
  @Input() appearance = '';
  @Input() modo = '';
  @Input() borderClass: boolean = false;
  @Input() label;
  @Input() totalDecimals = 2;
  @Input() placeholder ='';
  @Output() onSelectedChange = new EventEmitter<any>();
  inputControl: FormControl;

  _value = '';
  number: number = 0;
  @Input() set value(value) {
    this._value = value;
  };
  @Input() isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue( inputValue ): void {
    if (inputValue) {
        this._value = inputValue
    } else {
        this._value = '';
    }
  }
  registerOnChange(fn: any): void {
      this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
      this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
      this.isDisabled = isDisabled;
  }

  constructor(
    public _utls: UtilitiesService
  ) { }

  ngOnInit(): void {
  }

}
