import { Component, Input, OnInit, Output, EventEmitter, ViewChild, ElementRef, Optional, Inject } from '@angular/core';
import { BarcodeFormat } from '@zxing/library';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ModalService } from '../../../../services/dialog/modal.service';
import { DialogService } from '../../../../services/dialog/dialog.service';

@Component({
  selector: 'app-scanner',
  templateUrl: './scanner.component.html',
  styleUrls: ['./scanner.component.sass']
})
export class ScannerComponent implements OnInit {

  @ViewChild('scanner') scanner: ElementRef;
  @Output() onChangeScanner = new EventEmitter();
  @Input() valid = '';
  @Input() title = 'Escane codigo QR';
  @Input() mode = 'cover';
  @Input() messageValid = '';
  @Input() allowrdFormats = [
    BarcodeFormat.QR_CODE,
    BarcodeFormat.EAN_13,
    BarcodeFormat.CODE_128,
    BarcodeFormat.DATA_MATRIX
  ]
  disable = true;
  hasDevices: boolean;
  hasPermission: boolean;
  qrResult: any;
  currentDevice: MediaDeviceInfo = null;
  availableDevices: MediaDeviceInfo[];

  constructor(
    private _ds: DialogService,
    private _ms: ModalService,
    @Optional() @Inject(MAT_DIALOG_DATA) public data,
    public dialogRef: MatDialogRef<ScannerComponent>
  ) {
    if (data) {
      this.title = data.title;
      this.mode = data.mode;
      this.messageValid = data.messageValid;
      this.valid = data.valid;
    }
  }

  ngOnInit(): void {
  }

  onHasPermission(has: boolean): void {
    this.hasPermission = has;
  }
  // 	Emitted when some error occurs during the scan process.
  onScanError(error): void {
    console.log(error)
  }
  // Emitted when the scanner couldn't decode any result from the media stream.
  onScanFailure(error): void {
    console.log(error)
  }
  //Emits the result as string, after a valid QR code was scanned.
  onScanSucces(result: string): void {
    if (!this.valid) {
      this.disable = false;
      this.onChangeScanner.emit(result);
      this.dialogRef.close(result);
    } else if (result.includes(this.valid)) {
      this.disable = false;
      this.onChangeScanner.emit(result);
      this.dialogRef.close(result);
    } else {
      this._ds.showSnack(this.messageValid || 'El codigo QR no es valido.', null, 'error')
    }
  }
  // 	Emitted after any scan attempt, no matter what.
  onScanComplete(complete): void {

  }
}
