import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';
import { UsuarioService } from './usuario/usuario.service';
import { HttpClient } from '@angular/common/http';
import { URL } from '../config/url.services';

@Injectable({
  providedIn: 'root'
})
export class MenuService {

  _menu: any = [];
  menuRoutes: any = [];
  reportesPersonalizados: any = [];
  dashboard = [];

  private menuRoutesSubject = new Subject<any>();
  _menuRoutes$ = this.menuRoutesSubject.asObservable();

  constructor(
    private _us: UsuarioService,
    private _http: HttpClient
  ) { }

  currentPageName( path ){
    return this.menuRoutes.find(route => path.indexOf(route.path) >= 0) || '';
  }

  setFavorite(item) {
    let itemFound = this.menuRoutes.findIndex(route => item.id === route.id);
    if (itemFound >= 0) {
        this.menuRoutes[itemFound].favorite = !item.favorite;
    }
  }
  getChildsByParentId( parentId ) {
    if( !this.menuRoutes.length ) return;
    const categoryArray = this.menuRoutes.reduce((result, element) => {
        if ( parentId === element.parentId ) {
            let catIdx = result.findIndex(item => item.category === element.category);
            if (catIdx === -1) {
                result.push({ category: element.category, childs: [] });
                catIdx = result.length - 1;
            }
            if (element.favorite) {
                result[0].childs.push(element);
            }
            result[catIdx].childs.push(element);
        }
        return result;
    }, [{ category: 'Favoritos', childs: [] }]);
    if (categoryArray[0].childs.length === 0)
        categoryArray.splice(0, 1);
    categoryArray.forEach(item => item.childs.sort((a, b) => a.order - b.order));
    return categoryArray;
  }

  async getMenuSearch(){
    let response = <any>await this._http.get(`${URL}/usuario/${ this._us.usuario.idUsuario }/searchbar`).toPromise();
    return response;
  }

  async loadMenuSearch(){
    if( !this.menuRoutes.length ){
      this.menuRoutes = await this.getMenuSearch();
    }
    this.menuRoutesSubject.next( this.menuRoutes );
  }
}
