import { HttpErrorResponse } from '@angular/common/http';

export class HttpError {
    code: number;
    codeText: string;
    message: string;
    errors: any;
    errorCode: number;
    errorData: any;
    constructor(httpError) {

        const { status, statusText, error } = httpError;
        const errIsArray = Array.isArray(error);
        // errro puede ser array o objeto
        if (error?.code) {
            this.errorCode = error.code;
        }
        if (error?.data) {
            this.errorData = error.data;
        }
        if (errIsArray) {
            this.message = error[0];
            this.errors = error;
        } else {
            if (error?.errors) {
                this.message = error.errors[0];
                this.errors = error.errors;
            } else if (typeof error === 'string' || error instanceof String) {
                this.message = <string>error;
                this.errors = [error];
            }
        }
        this.code = status
        this.codeText = statusText;
    }
    public static parseHttpError(httpError: HttpErrorResponse) {
        let err;
        const { status, statusText, error } = httpError;
        const errIsArray = Array.isArray(error);
        err = {
            code: status,
            codeText: statusText,
            message: error ? errIsArray ? error[0] : error : null,
            errors: error ? errIsArray ? error : [error] : null,
        }
        //}
        return err;
    }
}
