import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { UsuarioService } from '../usuario/usuario.service';


@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {
  constructor(private _us: UsuarioService, private _router: Router) { }
  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {

    return new Promise(async (resolve) => {
      if (this._us.isAuthenticated()) {
        //await this._us.getMenu();
        //const perm = this._us.routes.find(item => item.url === state.url);
        // if (!perm) {
        //   this._router.navigateByUrl(this._us.menu[0]?.url || this._us.routes[0].url)
        // }
        resolve(true);
      } else {
        this._router.navigate(["/login"]);
        resolve(false);
      }
      //resolve(true)
    });
  }

}
