import { Component, OnInit, Input, EventEmitter, Output, forwardRef } from '@angular/core';

import { Subscription } from 'rxjs';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { MatSelectChange } from '@angular/material/select';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { TipoModalidad } from '../../../classes/modalidad.class';

@Component({
  selector: 'app-tipo-modalidad-dropdown',
  templateUrl: './tipo-modalidad-dropdown.component.html',
  styleUrls: ['./tipo-modalidad-dropdown.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoModalidadDropdownComponent),
      multi: true
    }
  ]
})
export class TipoModalidadDropdownComponent implements OnInit {

  tiposModalidades: TipoModalidad[] = [];
  @Input() placeholder: string;
  appearance;
  value: TipoModalidad;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipo: TipoModalidad): void {
    if (tipo) {
      this.value = tipo;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  ngOnDestroy(): void {
    this.tipoModalidadSubs.unsubscribe();
  }

  tipoModalidadSubs: Subscription;
  tipoModalidad$: Subscription;

  opciones: TipoModalidad[];
  @Output() onSelectedChange = new EventEmitter();

  constructor(
    private _cs: CatalogosService,
    private _ut: UtilitiesService
  ) { }

  ngOnInit(): void {
    this.appearance = this._ut.appearance;
    this.inicializar();
  }
  async inicializar() {
    this.tipoModalidadSubs = this._cs.tiposModalidad$.subscribe(tipos => {
      this.tiposModalidades = tipos;
      this.opciones = tipos
      if (this.value) {
        this.writeValue(this.value);
      }
    });
    await this._cs.loadTiposModalidad();
  }

  seleccionar(event: MatSelectChange) {
    let opt = this.opciones.find(item => item.idTipoModalidad === event.value);
    this.value = opt;
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(opt);
  }
  resetValue() {
    this.value = this.opciones[0];
  }

}
