import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { URL } from 'src/app/config/url.services';
import { UsuarioService } from '../usuario/usuario.service';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class EntradaService {

  constructor(private _http: HttpClient) { }

  async getEntradas(filters = null, sort = null, size = null, page = null) {
    let params = [];
    if (filters) params = filters ? filters.map(item => `${Object.keys(item)[0]}=${Object.values(item)[0].toString().replace("&", "%26")}`) : []
    if (sort) params.push("sort=" + sort);
    if (size) params.push("size=" + size);
    if (page) params.push("page=" + page);
    //escape &
    const queryStr = params.join("&");
    const response = await this._http.get(`${URL}/entrada${queryStr ? "?" + queryStr : ""}`).toPromise();
    return response;
  }

  async getEntrada(idEntrada) {
    const response = await this._http.get(`${URL}/entrada/${idEntrada}`).toPromise();
    return response;
  }
  async postEntrada(entrada) {
    const response = await this._http.post(`${URL}/entrada`, entrada).toPromise();
    return response;
  }
  async putEntrada(idEntrada, operacion) {
    const response = await this._http.put(`${URL}/entrada/${idEntrada}`, operacion).toPromise()
    return response;
  }
  async patchEntrada(idEntrada, operacion) {
    const response = await this._http.patch(`${URL}/entrada/${idEntrada}`, operacion).toPromise()
    return response;
  }
  async deleteEntrada(idEntrada) {
    const response = await this._http.delete(`${URL}/entrada/${idEntrada}`).toPromise()
    return response;
  }
  async postEntradaBulto(idEntrada, bulto) {
    const response = await this._http.post(`${URL}/entrada/${idEntrada}/bulto`, bulto).toPromise();
    return response;
  }
  async putEntradaBulto(idEntrada, idEntradaBulto, bulto) {
    const response = await this._http.put(`${URL}/entrada/${idEntrada}/bulto/${idEntradaBulto}`, bulto).toPromise()
    return response;
  }
  async deleteEntradaBulto(idEntrada, idEntradaBulto) {
    const response = await this._http.delete(`${URL}/entrada/${idEntrada}/bulto/${idEntradaBulto}`).toPromise()
    return response;
  }

  transformEntrada(entrada) {

    return {
      ...entrada,
      fechaArriboDisplay: entrada.fechaArribo ? moment(entrada.fechaArribo).format("DD-MM-YYYY HH:mm") : null,
      fechaVencimientoInbondDisplay: entrada.fechaVencimientoInbond ? moment(entrada.fechaVencimientoInbond).format("DD-MM-YYYY HH:mm") : null,
      fechaArribo: entrada.fechaArribo ? moment(entrada.fechaArribo) : null,
      fechaVencimientoInbond: entrada.fechaVencimientoInbond ? moment(entrada.fechaVencimientoInbond) : null,
      Bultos: entrada.Bultos.map(itm => this.transformBulto(itm))
    }
  }
  transformBulto(bulto) {

    return { ...bulto, dimensiones: `${Number(bulto.largo) || " "} x ${Number(bulto.ancho) || " "} x ${Number(bulto.alto) || " "}`, tipoBulto: bulto?.TipoBulto?.nombre, seccion: bulto?.Seccion?.nombre }

  }

  async generarEntrada(idEntrada) {
    const response = await this._http.get(`${URL}/entrada/${idEntrada}/generarEntrada`, { responseType: "blob", observe: 'response' }).toPromise();
    return response;
  }
  async generarEntradaQR(idEntrada) {
    const response = await this._http.get(`${URL}/entrada/${idEntrada}/qr`, { responseType: "blob", observe: 'response' }).toPromise();
    return response;
  }
}
