import { Component, OnInit, forwardRef, EventEmitter, Output, Input, OnDestroy } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { MatSelectChange } from '@angular/material/select';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-puesto-dropdown',
  templateUrl: './puesto-dropdown.component.html',
  styleUrls: ['./puesto-dropdown.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PuestoDropdownComponent),
      multi: true
    }
  ]
})
export class PuestoDropdownComponent implements OnInit,OnDestroy {

  @Input() idDepartamento$ = new EventEmitter();
  puestos = [];
  @Input() placeholder: string;
  appearance;
  value;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipo): void {
    if (tipo) {
        this.value = tipo;
    } else {
        this.value = null;
    }
  }
  registerOnChange(fn: any): void {
      this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
      this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
      this.isDisabled = isDisabled;
  }

  ngOnDestroy(): void {
      this.puestoSubs.unsubscribe();
      this.idDepartamento$.unsubscribe();
  }

  puestoSubs   : Subscription;
  TipoConcepto$ : Subscription;
  idDepartamentoSubs: Subscription;

  opciones;
  opcionesFiltradas;
  @Output() onSelectedChange = new EventEmitter();

  constructor(
      private _cs: CatalogosService,
      private _ut:UtilitiesService
  ) { }

  ngOnInit(): void {
      this.appearance     = this._ut.appearance;
      this.inicializar();

      this.idDepartamentoSubs = this.idDepartamento$.subscribe( idDepartamento=>{
        if(this.opciones){
          this.opcionesFiltradas = this.opciones.filter(op => op.idDepartamento === idDepartamento)
        }

      })
  }
  async inicializar() {
    this.puestoSubs = this._cs.puesto$.subscribe(tipos => {
        this.puestos = tipos;
        this.opciones = tipos
        if (this.value) {
          this.opcionesFiltradas = tipos
          this.writeValue(this.value);
        }
    });
    await this._cs.loadPuesto();
  }

  seleccionar(event: MatSelectChange) {
      let opt = this.opcionesFiltradas.find(item => item.idPuesto === event.value);
      this.value = opt;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(opt);
  }
  resetValue() {
      this.value = this.opciones[0];
  }


}
