import { Component, OnInit } from '@angular/core';
import { UsuarioService } from '../../../services/usuario/usuario.service';
import { BreakpointObserver } from '@angular/cdk/layout';
import { NavService } from '../../../services/nav/nav.service';
import { Subscription } from 'rxjs';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { filter } from 'rxjs/operators';
import { MenuService } from '../../../services/menu.service';

@Component({
  selector: 'app-sidenav',
  templateUrl: './sidenav.component.html',
  styleUrls: ['./sidenav.component.sass']
})
export class SidenavComponent implements OnInit {

  menu;
  opened: boolean = true;
  sideNavOpen$: Subscription;
  routerEvents$: Subscription;
  pathName: string;

  constructor(
    private _ns: NavService,
    private bkpoint: BreakpointObserver,
    private _us: UsuarioService,
    private _ms: MenuService,
    private _router: Router
  ) { }

  ngOnInit(): void {

    this.sideNavOpen$ = this._ns.sideNavOpened.subscribe(change =>{
      this.opened = change
    });

    this.routerEvents$ = this._router.events
    .pipe(
      filter(event => event instanceof NavigationEnd)
    ).subscribe(( event: NavigationEnd ) =>{
      this.pathName = event.url;
    });

    this.pathName = this._router.url;
    this.opened = this._ns.isSideNavOpened;
    this.getMenu();
  }

  async getMenu() {
    try {
      let menu = <any>await this._us.getMenu();
      this.menu = menu;
      console.log(this.menu);
    } catch (error) {
      console.log(error);
    }
  }

  openDropdown( ruta ){
    ruta.expand = !ruta.expand;
  }

  handleCloseSideNav(){
    this._ns.menuToggle();
  }
}
