import { Component, OnInit, forwardRef, Output, Input, EventEmitter } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { Subscription } from 'rxjs';
import { ConceptoFactura } from 'src/app/shared/classes/concepto-factura.class';


@Component({
  selector: 'app-concepto-factura-autocomplete',
  templateUrl: './concepto-factura-autocomplete.component.html',
  styleUrls: ['./concepto-factura-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ConceptoFacturaAutocompleteComponent),
      multi: true
    }
  ]
})
export class ConceptoFacturaAutocompleteComponent implements OnInit {

  @Input() modo;
  @Input() set idUnidadNegocio(value) {
    this._idUnidadNegocio = value;
    this.getConceptos();
  }
  _idUnidadNegocio;
  @Input() set value(value) {
    this._value = value;
  }
  @Output() onSelectedChange = new EventEmitter<ConceptoFactura>();
  @Input() appearance;
  @Input() clearButton = false;
  @Input() idTipoConcepto;
  opciones: ConceptoFactura[];
  opciones$: Subscription;
  opcionesFiltradas = [];
  _value: ConceptoFactura;
  @Input() isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(concepto: ConceptoFactura): void {
    if (concepto) {
      this.value = concepto;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(e, value: string) {

    this.opcionesFiltradas = <any[]>await this.opciones.filter(opt => opt.idUnidadNegocio === this._idUnidadNegocio && opt.nombre.toLowerCase().indexOf(value.toLowerCase()) >= 0).slice(0, 10);
  }
  constructor(public _cs: CatalogosService) {

  }
  ngOnDestroy(): void {
    if (this.opciones$) {
      this.opciones$.unsubscribe();
    }
  }
  handleBlur(e, value: string) {

    let original = this._value?.nombre || "";
    if (value !== original) {
      if (!value) {
        if (this._value) {
          this.seleccionar(null, { blur: true })
        }
      } else {
        let concepto = this.opcionesFiltradas.find(itm => itm.nombre.toString().toLocaleLowerCase() === value.toString().toLocaleLowerCase());
        if (concepto) {
          this.seleccionar(concepto, { blur: true })
        } else {
          if (!this._value) {
            // e.target.value = "";
            // this.onInput( { blur: true },"")
            // if (this._value) {
            //   this.seleccionar(null, { blur: true })
            // }
          } else {
            e.target.value = this._value?.nombre || "";
          }
        }
      }
    }
  }
  ngOnInit() {
    this.inicializar();
  }
  isLoading = false;
  async inicializar() {
    this.opciones$ = this._cs.conceptoFactura$.subscribe(conceptoFacturas => {
      // get por tipo de 
      this.opciones = conceptoFacturas.filter(itm => itm.idTipoConcepto === this.idTipoConcepto);
      //this.opcionesFiltradas = conceptoFacturas.filter(itm => itm.idTipoConcepto === this.idTipoConcepto);
      this.isLoading = false;
    });
    this.isLoading = true
    await this._cs.loadConceptoFactura();
  }
  getConceptos() {
    if (this.idTipoConcepto && this._idUnidadNegocio) {
      this.opcionesFiltradas = this.opciones.filter(itm => itm.idTipoConcepto === this.idTipoConcepto && itm.idUnidadNegocio === this._idUnidadNegocio);
    } else {
      this.opcionesFiltradas = [];
      if (this._value) {
        //this.handleClear();
      }
    }
  }
  seleccionar(concepto: ConceptoFactura, event) {
    if (event.isUserInput || event.blur) {
        this.value = concepto;
        this.onTouch();
        this.onChange(this._value);
        this.onSelectedChange.emit(concepto);
    }
  }
  handleClear() {
    this._value = null;
    this.onTouch();
    this.onChange(this._value);
    this.onSelectedChange.emit(null);
  }
  displayFn(option: ConceptoFactura): any {
    return option && option.nombre ? option.nombre : '';
  }
}
