import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.sass']
})
export class SearchBarComponent implements OnInit {

  @Input() opciones;
  @Input() buttonTitle;
  @Input() placeholder
  @Output() buscarClickEmitter = new EventEmitter();
  //searchForm: FormGroup;
  searchForm = new FormGroup({ valor: new FormControl(null) })
  @Input() set value(value) {
    //this._value = value;
    this.searchForm.patchValue({ valor: value })
  };
  //_value;
  constructor() { }

  ngOnInit(): void {
    if (this.opciones && this.opciones.length > 0) {
      let opcion1 = this.opciones[0];
      this.searchForm.addControl("tipoFiltroSeleccionado", new FormControl(opcion1));
    }
  }

  async handleRastrearClick() {
    try {

      this.buscarClickEmitter.emit(this.searchForm.value)


    } catch (error) {
      console.log(error);
    } finally {
    }
  }
  handleTipoFiltroClick(tipoFiltro) {
    if (this.opciones && this.opciones.length > 0) {
      this.searchForm.get("tipoFiltroSeleccionado").setValue(tipoFiltro);
    }
  }

}
