import { Component, OnInit, forwardRef, EventEmitter, Output, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { TipoConcepto } from '../../../interface/TipoConcepto.interface';
import { Subscription } from 'rxjs';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { MatSelectChange } from '@angular/material/select';

@Component({
  selector: 'app-tipo-concepto-dropdown',
  templateUrl: './tipo-concepto-dropdown.component.html',
  styleUrls: ['./tipo-concepto-dropdown.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoConceptoDropdownComponent),
      multi: true
    }
  ]
})
export class TipoConceptoDropdownComponent implements OnInit {

  tiposConceptos: TipoConcepto[] = [];
  @Input() placeholder: string;
  appearance;
  value: TipoConcepto;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipo: TipoConcepto): void {
    if (tipo) {
      this.value = tipo;
    } else {
      this.value = null;
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }

  ngOnDestroy(): void {
    this.tipoConceptoSubs.unsubscribe();
  }

  tipoConceptoSubs: Subscription;
  TipoConcepto$: Subscription;

  opciones: TipoConcepto[];
  @Output() onSelectedChange = new EventEmitter();

  constructor(
    private _cs: CatalogosService,
    private _ut: UtilitiesService
  ) { }

  ngOnInit(): void {
    this.appearance = this._ut.appearance;
    this.inicializar();
  }
  async inicializar() {
    this.tipoConceptoSubs = this._cs.tiposConcepto$.subscribe(tipos => {
      this.tiposConceptos = tipos;
      this.opciones = tipos
      if (this.value) {
        this.writeValue(this.value);
      }
    });
    await this._cs.loadTiposConceptos();
  }

  seleccionar(event: MatSelectChange) {
    let opt = this.opciones.find(item => item.idTipoConcepto === event.value);
    this.value = opt;
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(opt);
  }
  resetValue() {
    this.value = this.opciones[0];
  }

}
