import { Component, OnInit, EventEmitter, Output, Input, forwardRef } from '@angular/core';
import { debounceTime, Subscription } from 'rxjs';
import { NG_VALUE_ACCESSOR, FormControl } from '@angular/forms';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { CATEstado } from '../../../interface/CATEstado.interface';


@Component({
  selector: 'app-estado-autocomplete',
  templateUrl: './estado-autocomplete.component.html',
  styleUrls: ['./estado-autocomplete.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => EstadoAutocompleteComponent),
      multi: true
    }
  ]
})
export class EstadoAutocompleteComponent implements OnInit {


  estadoCtrl: FormControl = new FormControl();
  @Output() onSelectedChange = new EventEmitter<CATEstado>();
  @Input() placeholder: string
  appearance;
  @Input() clearButton = false;
  estado$: Subscription;
  opciones: CATEstado[] = []
  opcionesFiltradas: CATEstado[] = []
  value: CATEstado;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(pais: CATEstado): void {
    if (pais) {
      this.estadoCtrl.patchValue(pais);
    } else {
      this.estadoCtrl.patchValue(null);
    }
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    this.isDisabled = isDisabled;
  }
  async onInput(value: string) {
    if (typeof (value) === 'string') {
      this.opcionesFiltradas = <any[]>await this._cs.getCatalogo('estados',
        [{ 'filters[nombre][like]': `%25${value}%25` }],
        'nombre,asc',
        10,
        0
      );
    }
  }
  constructor(
    private _cs: CatalogosService,
    private _utls: UtilitiesService,
  ) {

  }
  ngOnDestroy(): void {
    if (this.subs) {
      this.subs.unsubscribe();
    }
  }

  subs: Subscription;
  ngOnInit() {
    this.inicializar();
    this.appearance = this._utls.appearance;

    this.subs = this.estadoCtrl.valueChanges.pipe(
      debounceTime(400)
    ).subscribe(text => this.onInput(text))
  }
  isLoading = false;
  async inicializar() {

  }
  seleccionar(pais: CATEstado, event) {
    if (event.isUserInput) {
      this.value = pais;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(pais);
    }
  }
  handleClear() {
    this.estadoCtrl.patchValue(null);
    this.onTouch();
    this.onChange(this.value);
    this.onSelectedChange.emit(null);
  }
  displayFn(option: CATEstado): any {
    return option && option.nombre
      ? `${option.nombre}, ${option.Pais.nombre}`
      : '';
  }

}
