import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'app-card-default',
  templateUrl: './card-default.component.html',
  styleUrls: ['./card-default.component.sass']
})
export class CardDefaultComponent implements OnInit {

  @Input() title: string = '';
  @Input() iconButton: string = '';
  @Output() onClick = new EventEmitter();
  @Output() onCopiarClick = new EventEmitter();
  @Output() onVincularClick = new EventEmitter();

  @Input() noPadding: boolean = false;
  @Input() copiar = false;
  @Input() vincular = false;

  constructor() { }

  ngOnInit(): void {
  }

  handleClickAction() {
    this.onClick.emit();
  }
  handleCopiarClick() {
    this.onCopiarClick.emit();
  }
  handleVincularClick() {
    this.onVincularClick.emit();
  }
}
