import { Component, OnInit, forwardRef, EventEmitter, Output, Input } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { MatSelectChange } from '@angular/material/select';
import { Subscription } from 'rxjs';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';

@Component({
  selector: 'app-oficina-dropdown',
  templateUrl: './oficina-dropdown.component.html',
  styleUrls: ['./oficina-dropdown.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => OficinaDropdownComponent),
      multi: true
    }
  ]
})
export class OficinaDropdownComponent implements OnInit {

  oficinas = [];
  @Input() placeholder: string;
  appearance;
  value;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipo): void {
    if (tipo) {
        this.value = tipo;
    } else {
        this.value = null;
    }
  }
  registerOnChange(fn: any): void {
      this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
      this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
      this.isDisabled = isDisabled;
  }

  ngOnDestroy(): void {
      this.oficinaSubs.unsubscribe();
  }

  oficinaSubs   : Subscription;
  TipoConcepto$ : Subscription;

  opciones;
  @Output() onSelectedChange = new EventEmitter();

  constructor(
      private _cs: CatalogosService,
      private _ut:UtilitiesService
  ) { }

  ngOnInit(): void {
      this.appearance     = this._ut.appearance;
      this.inicializar();
  }
  async inicializar() {
    this.oficinaSubs = this._cs.oficina$.subscribe(tipos => {
        this.oficinas = tipos;
        this.opciones = tipos
        if (this.value) {
            this.writeValue(this.value);
        }
    });
    await this._cs.loadOficina();
  }

  seleccionar(event: MatSelectChange) {
      let opt = this.opciones.find(item => item.idOficina === event.value);
      this.value = opt;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(opt);
  }
  resetValue() {
      this.value = this.opciones[0];
  }


}
