  calcularImportes(operacion) {
    let suma = 0;
    let sumaDls = 0;
    let liquidacion = [];
    operacion.TraficoGastos.forEach(concepto => {
      let entidad = liquidacion.find(itm => itm.idFacturaA === concepto.idFacturaA && itm.idExpide === concepto.idTipoUnidadNegocio && itm.idMoneda === concepto.idMoneda);

      if (!entidad) {
        liquidacion.push({ idFacturaA: concepto.idFacturaA, FacturaA: concepto.FacturaA, idExpide: concepto.idTipoUnidadNegocio, Expide: concepto.TipoUnidadNegocio, idMoneda: concepto.idMoneda, total: 0 })
        entidad = liquidacion[liquidacion.length - 1];
      }
      entidad.total = parseFloat(entidad.total) + parseFloat(concepto.total);
      // let expide = entidad.Expide.find(itm => itm.idTipoUnidadNegocio === concepto.idTipoUnidadNegocio)
      // if (!expide) {
      //   entidad.Expide.push({ idTipoUnidadNegocio: concepto.idTipoUnidadNegocio, TipoUnidadNegocio: concepto.TipoUnidadNegocio, Monedas: [] });
      //   expide = entidad.Expide[entidad.Expide.length - 1]
      // }

      // let moneda = expide.Monedas.find(itm => itm.idMoneda === concepto.idMoneda)
      // if (!moneda) {
      //   expide.Monedas.push({ idMoneda: concepto.idMoneda, totalGastos: 0, totalCostos: 0 })
      //   moneda = expide.Monedas[expide.Monedas.length - 1]
      // }
      // moneda.totalGastos = parseFloat(moneda.totalGastos) + parseFloat(concepto.total);
      if (concepto.idMoneda === "MXP") {
        // suma += parseFloat(concepto.importe);
        suma += parseFloat(concepto.total);
      } else {
        // sumaDls += parseFloat(concepto.importe);
        sumaDls += parseFloat(concepto.total);
      }
    });

    operacion.gastos = suma;
    operacion.gastosDls = sumaDls;
    operacion.gastosMxp = suma;
    suma = 0;
    sumaDls = 0;

    operacion.TraficoCostos.forEach(concepto => {

      // let entidad = liquidacion.find(itm => itm.idFacturaA === concepto.idProveedor)

      // if (!entidad) {
      //   liquidacion.push({ idFacturaA: concepto.idProveedor, FacturaA: concepto.Proveedor, Expide: [] })
      //   entidad = liquidacion[liquidacion.length - 1];
      // }
      // let expide = entidad.Expide.find(itm => itm.idTipoUnidadNegocio === concepto.idTipoUnidadNegocio)
      // if (!expide) {
      //   entidad.Expide.push({ idTipoUnidadNegocio: concepto.idTipoUnidadNegocio, TipoUnidadNegocio: concepto.TipoUnidadNegocio, Monedas: [] });
      //   expide = entidad.Expide[entidad.Expide.length - 1]
      // }

      // let moneda = expide.Monedas.find(itm => itm.idMoneda === concepto.idMoneda)
      // if (!moneda) {
      //   expide.Monedas.push({ idMoneda: concepto.idMoneda, totalGastos: 0, totalCostos: 0 })
      //   moneda = expide.Monedas[expide.Monedas.length - 1]
      // }
      // moneda.totalCostos = parseFloat(moneda.totalCostos) + parseFloat(concepto.importe);
      if (concepto.idMoneda === "MXP") {
        // suma += parseFloat(concepto.importe);
        suma += parseFloat(concepto.importe);
      } else {
        // sumaDls += parseFloat(concepto.importe);
        sumaDls += parseFloat(concepto.importe);
      }
    });
    operacion.costos = suma;
    operacion.costosDls = sumaDls;
    operacion.costosMxp = suma;
    // operacion.costosMxp = suma + (sumaDls * (operacion.tipoCambioInterbancario ? operacion.tipoCambioInterbancario : 1));

    operacion.utilidad = operacion.gastos - operacion.costos
    operacion.utilidadDls = operacion.gastosDls - operacion.costosDls
    operacion.utilidadMxp = operacion.gastosMxp - operacion.costosMxp
    // if (operacion.TraficoCostos.find(itm => itm.aplicaCompraDolares)) operacion.aplicaCompraDolares = true;
    // else operacion.aplicaCompraDolares = false;
    operacion.liquidacion = liquidacion;
  }
  ordenarEventos(operacion) {

    let eventos = JSON.parse(JSON.stringify(this._cs.catalogos.traficoEventos));
    eventos = eventos.sort((a, b) => a.orden - b.orden);

    eventos.forEach(evento => {

      let ev = operacion.TraficoEventos.find(itm => itm.idEvento === evento.idEvento)
      if (ev) {
        evento.fecha = moment(ev.fecha).format("DD/MM/YYYY HH:mm");
        evento.idReferenciaEvento = ev.idReferenciaEvento
        evento.fechaInput = ev.fecha;
        //evento.tipoCaptura = ev.tipoCaptura;
      }
    });
    return eventos;
  }
