import { Component, OnInit, forwardRef, Input, EventEmitter, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { TipoContacto } from '../../../interface/TipoContacto';
import { MatSelectChange } from '@angular/material/select';
import { CatalogosService } from '../../../../services/catalogos/catalogos.service';
import { UtilitiesService } from '../../../../services/utilities/utilities.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-tipo-contacto-dropdown',
  templateUrl: './tipo-contacto-dropdown.component.html',
  styleUrls: ['./tipo-contacto-dropdown.component.sass'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TipoContactoDropdownComponent),
      multi: true
    }
  ]
})
export class TipoContactoDropdownComponent implements OnInit {

  @Input() placeholder: string;
  appearance;
  value:  TipoContacto;
  isDisabled: boolean;
  onChange = (_: any) => { };
  onTouch = () => { };
  writeValue(tipo:  TipoContacto): void {
    if (tipo) {
        this.value = tipo;
    } else {
        this.value = null;
    }
  }
  registerOnChange(fn: any): void {
      this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
      this.onTouch = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
      this.isDisabled = isDisabled;
  }

  ngOnDestroy(): void {
      this.tipoContacto$.unsubscribe();
  }

  tipoContacto$ : Subscription;

  opciones: TipoContacto[];
  @Output() onSelectedChange = new EventEmitter();

  constructor(
      private _cs: CatalogosService,
      private _ut:UtilitiesService
  ) { }

  ngOnInit(): void {
      this.appearance     = this._ut.appearance;
      this.inicializar();
  }
  async inicializar() {
    this.tipoContacto$ = this._cs.tipoContacto$.subscribe(tipos => {

        this.opciones = tipos

        if (this.value) {
            this.writeValue(this.value);
        }
    });
    await this._cs.loadTipoContacto();
  }

  seleccionar(event: MatSelectChange) {
      let opt = this.opciones.find(item => item.idTipoContacto === event.value);
      this.value = opt;
      this.onTouch();
      this.onChange(this.value);
      this.onSelectedChange.emit(opt);
  }
  resetValue() {
      this.value = this.opciones[0];
  }

}
